<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeStart"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            :default-time="['12:00:00']"
          ></el-date-picker>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <el-button type="primary" plain></el-button>
        <el-table
          :data="tableData"
          style="width: 100%;marginTop:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="id" label="ID" sortable min-width="80"></el-table-column>
          <el-table-column prop="name" label="" min-width="120"></el-table-column>
          <el-table-column prop="phone" label="" min-width="180"></el-table-column>
          <el-table-column prop="feedbackTime" label="" min-width="180" sortable></el-table-column>
          <el-table-column prop="feedbackInfo" label="" min-width="180" sortable></el-table-column>
          <el-table-column label="" min-width="120">
            <template slot-scope="scope">
              <a class="operation-btn" @click="dialogFeedback=true"></a>
              <a class="operation-btn"></a>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">11010</div>
          <div class="pagination-box">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="100"
              layout="prev, pager, next, jumper"
              :total="1000"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogFeedback" width="786">
      <ul class="dialogFeedback-info">
        <li>
          <div class="info-title"></div>
          <div class="info-content">18277776666</div>
        </li>
        <li>
          <div class="info-title"></div>
          <div class="info-content">2019-03-03 11:11:42</div>
        </li>
        <li>
          <div class="info-title"></div>
          <div class="info-content"></div>
        </li>
        <li>
          <div class="info-title"></div>
          <div class="info-content">
            <div class="img-small" @click="clickImg($event)">
              <img src="../../../../assets/images/lake.jpg" alt>
              <a class="img-btn"></a>
            </div>
          </div>
        </li>
      </ul>
    </el-dialog>
    <!---->
    <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>
  </div>
</template>

<script>
import BigImg from "@/components/common/BigImg.vue";
export default {
  data() {
    return {
      dialogFeedback: false,
      timeStart: "",
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [
        {
          id: "1",
          name: "will",
          phone: "18277776666",
          feedbackTime: "2018-10-23 09:17:30",
          feedbackInfo: "7"
        }
      ],
      showImg: false
    };
  },
  components: {
    BigImg
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    //
    clickImg(e) { 
      this.showImg = true;
      this.imgSrc = e.currentTarget.children[0].src;
    },
    //
    viewImg() {
      this.showImg = false;
    }
  }
};
</script>

<style scoped lang="scss">
.dialogFeedback-info {
  > li {
    display: flex;
    margin-bottom: 32px;
    .info-title {
      color: #333333;
      font-size: 20px;
      line-height: 26px;
      width: 100px;
      font-weight: 400;
      text-align: right;
    }
    .info-content {
      flex: 1;
      margin-left: 23px;
      font-size: 20px;
      color: #666;
      font-weight: 400;
      line-height: 26px;
    }
  }
}

</style>




