<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeModel"
            @change="pickTime"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span></span>
            <el-cascader
              v-model="area"
              :options="options"
              :show-all-levels="false"
              :filterable="true"
              :clearable="true"
            ></el-cascader>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder="" @keyup.enter.native="shopList('reset', true)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="shopList('reset', true)"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <div class="operationBox">
          <el-button type="primary" plain @click="deleteBtn(ids)"></el-button>
          <div class="citySelect">
            <span>{{total}}</span>
            <el-select clearable v-model="city" placeholder="" @change="shopList('reset', true)">
              <el-option
                v-for="item in cityOptions"
                :key="item.label"
                :label="item.label"
                :value="item.label"
              ></el-option>
            </el-select>
          </div>
        </div>
        <el-table
          :data="tableData" 
          style="width: 100%;marginTop:20px;" 
          @expand-change="dishList" 
          :row-key="getRowKeys"
          :expand-row-keys="expands"
          v-loading="loading"
          @selection-change="selectChange"
        >
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column type="expand" align="center">
            <template slot-scope="props">
              <table class="table-expand">
                <thead>
                  <tr>
                    <td width="200px"></td>
                    <td width="200px"></td>
                    <td width="200px"></td>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.row.dishList" :key="index">
                    <td class="expandSkuName">{{item.skuName}}</td>
                    <td>{{item.orderToday}}</td>
                    <td>{{item.orderHistory}}</td>
                  </tr>
                </tbody>
              </table>
            </template>
          </el-table-column>
          <el-table-column label="ID" prop="id" align="center" width="70">
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column label="" prop="shopName" align="center"></el-table-column>
          <el-table-column label="" prop="mobile" align="center"></el-table-column>
          <el-table-column label="" prop="settlingTime" align="center" sortable></el-table-column>
          <el-table-column label="" prop="address" align="center" width="300"></el-table-column>
          <el-table-column label="" prop="orderToday" align="center" sortable :sort-method="sortByOrderT"></el-table-column>
          <el-table-column label="" prop="orderHistory" align="center" sortable :sort-method="sortByOrderH"></el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="shopInfo(scope.$index)"></el-button>
              <el-button type="text" size="small" @click="deleteBtn(scope.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{total}}{{pages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="changePage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="dishBox">
      <div class="dishBox_tit"></div>
      <div class="dishList" v-loading="dishLoading">
        <div class="dishList_inner">
          <div class="dishItem" v-for="(item, index) in carousel" :key="index">
            <div class="dishItem_cont">
              <img :src="item.skuPic ? item.skuPic : ''" alt>
              <div class="cont_info">
                <span>{{item.skuName}}</span>
                <span>{{item.orderHistory}}</span>
              </div>
            </div>
            <div class="dishItem_oper">
              <div class="oper_edit" @click="goodsDetail(item.id, index)"></div>
              <div class="oper_dele" @click="delGoodsDialog(item.id, index)"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="deleteVisible" width="540px" v-loading="delLoading">
      <div class="dialog-info"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteVisible = false"> </el-button>
        <el-button type="primary" @click="deleteShop"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editVisible" width="50%" v-loading="shopInfoLoading">
      <div class="dialog_content">
        <el-form ref="form" label-width="150px" :model="shopObj">
          <el-form-item label="">
            <el-input type="textarea" v-model="shopObj.shopName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="shopObj.mobile"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="shopObj.address"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="shopObj.adminMobile"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div class="cashierList">
              <div v-if="shopObj.shopCashier.length > 0" v-for="(item, index) in shopObj.shopCashier" :key="index" class="cashierItem">
                <div>{{item.cashierName}}</div>
                <i class="el-icon-error" @click="deleteCashierDialog(item.id)"></i>
              </div>
              <i class="addIcon el-icon-circle-plus" @click="addCashierVisible = true"></i>
            </div>
            <div></div>
          </el-form-item>
          <el-form-item label="">
            <div v-if="shopObj.shopOwnerMap.length > 0">
              <div class="dia_item_Img" v-for="(item, index) in shopObj.shopOwnerMap" :key="index">
                <img :src="item.picUrl" alt>
                <i class="el-icon-error" @click="delGoodsImg(item)"></i>
              </div>
            </div>
            <el-upload
              v-if="shopObj.shopOwnerMap.length < 5"
              ref="upload"
              :show-file-list="false"
              :action="action"
              :headers="{Authorization: token}"
              list-type="picture-card"
              :on-success="successUpload"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false;"> </el-button>
        <el-button type="primary" @click="updateShop"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="addCashierVisible" width="540px" v-loading="addCashierLoading">
      <el-form ref="form" label-width="150px">
        <el-form-item label="">
          <el-input type="textarea" v-model="addCachierTel"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCashierVisible = false"> </el-button>
        <el-button type="primary" @click="addCashier"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="delCashierVisible" width="540px" v-loading="delCashierLoading">
      <div class="dialog-info"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delCashierVisible = false"> </el-button>
        <el-button type="primary" @click="deleteCashier"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="goodsVisible" width="50%" v-loading="goodsInfoLoading">
      <div class="dialog_content">
        <el-form ref="form" label-width="150px" :model="goodsObj">
          <el-form-item label="">
            <el-input type="textarea" v-model="goodsObj.skuName"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="goodsObj.sellPrice"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div class="shopMainImg">
              <img :src="goodsObj.skuPic" alt="">
              <div class="imgUpload">
                <el-upload
                  ref="upload"
                  :show-file-list="false"
                  :action="action"
                  :headers="{Authorization: token}"
                  list-type="picture-card"
                  :before-upload="mainImgBefore"
                  :on-success="mainImgUpload"
                >
                  <i class="el-icon-plus"></i>
                </el-upload>
              </div>
            </div>
          </el-form-item>
          <el-form-item label="">
            <div v-if="goodsObj.goodsSkuPicsList.length > 0">
              <div v-for="(item, index) in goodsObj.goodsSkuPicsList" :key="index" class="dia_item_Img">
                <img :src="item.picUrl" alt>
                <i class="el-icon-error" @click="delGoodsSkuPic(index, item.id)"></i>
              </div>
            </div>
            <el-upload
              v-if="goodsObj.goodsSkuPicsList.length < 5"
              ref="upload"
              :show-file-list="false"
              :action="action"
              :headers="{Authorization: token}"
              list-type="picture-card"
              :before-upload="mainImgBefore"
              :on-success="goodsImgUpload"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="goodsObj.contentText" style="width: 90%;" maxlength="200"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div>
              <div v-for="(item, index) in goodsObj.contentImgs" :key="index" class="dia_item_Img">
                <img :src="item" alt>
                <i class="el-icon-error" @click="delContentImgs(item)"></i>
              </div>
            </div>
            <el-upload
              v-if="goodsObj.contentImgs.length < 5"
              ref="upload"
              :show-file-list="false"
              :action="action"
              :headers="{Authorization: token}"
              list-type="picture-card"
              :before-upload="mainImgBefore"
              :on-success="contentPicUpload"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="goodsVisible=false"> </el-button>
        <el-button type="primary" @click="updateGoods"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="delGoodsVisible" width="540px" v-loading="delGoodsLoading">
      <div class="dialog-info"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delGoodsVisible = false"> </el-button>
        <el-button type="primary" @click="delGoods"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import api from "../../../../assets/js/api";
import common from "../../../../assets/js/common.js";
export default {
  data() {
    return {
      expands: [],        //id
      loading: true,      //
      delLoading: false,  //
      dishLoading: true,  //
      addCashierLoading: false,  //
      delCashierLoading: false,  //
      shopInfoLoading: false,    //
      goodsInfoLoading: false,   //
      delGoodsLoading: false,    //
      editVisible: false,    //
      deleteVisible: false,  //
      addCashierVisible: false,  //
      delCashierVisible: false,  //
      goodsVisible: false,       //
      delGoodsVisible: false,    //
      page: 1,
      rows: 10,
      pages: 1,
      total: 1,
      currentPage: 1,
      timeModel: "",         
      beginTime: "",         //
      endTime: "",           //
      city: "",              //
      position: "",          //
      id: "",                //id
      ids: "",               //id
      token: "",             //token
      action: "/api/manage/img/upload",        //
      addCachierTel: "",                       //
      shopId: "",                              //id
      shopIdx: 0,                              //
      cashierId: "",                           //id
      goodsId: "",                             //id
      filters: {
        name: ""
      },
      shopObj: {
        id: "",
        shopName: "",
        mobile: "",
        address: "",
        adminMobile: "",
        shopCashier: [],
        shopOwnerMap: []
      },
      goodsObj: {
        id: "",
        skuName: "",
        sellPrice: "",
        skuPic: "",
        smallPicUrl: "",
        goodsSkuPicsList: [], //
        goodsSkuPics: "", //id
        spuId: "",
        content: "", //
        contentText: "", //
        contentImgs: [] //
      },
      area: [],
      options: [],
      cityOptions: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      tableData: [],
      carousel: [],
      goodsContent: [],
      getRowKeys(row) {
        return row.id;
      }
    };
  },
  watch: {
    area(val) {      //
      if (val.length == 2) {
        let idx1 = val[0],
          idx2 = val[1];
        this.position = this.options[idx1].children[idx2].label;
      } else {
        this.position = "";
      }
      this.loading = true;
      this.shopList("reset", true);
    }
  },
  methods: {
    areaOption() {     //
      api
        .areaList()
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data && data.length > 0) {
            let obj = {};
            data && data.forEach((e, i) => {
              obj = {
                value: i,
                label: e.city,
                children: []
              };
              e.shopZoneItem && e.shopZoneItem.forEach((k, j) => {
                obj.children.push({
                  value: j,
                  label: k.name
                });
              });
              this.options.push(obj);
            });
          }
        })
        .catch(err => {});
    },
    shopList(types, dishflag) {    //
      if (types == "reset") {
        this.loading = true;
        this.page = 1;
      }
      api
        .shopList({
          beginTime: this.beginTime,
          endTime: this.endTime,
          position: this.position,
          keywords: common.blank(this.filters.name),
          city: common.isNull(this.city, "str"),
          page: this.page,
          rows: this.rows
        })
        .then(res => {
          let data = res.data;
          if (data) {
            this.pages = data.pages;
            this.total = data.total;
            this.tableData = data.list ? this.operateList(data.list) : [];
            if(this.tableData.length <= 0) this.carousel = [];
            if(dishflag) {
              this.shopId = this.tableData[0].id;
              this.shopIdx = 0;
              this.dishLoading = true;
              this.getDish(this.shopId, this.shopIdx);
            }
          }
          this.loading = false;
        })
        .catch(err => {
          this.loading = false;
        });
    },
    dishList(row, expandedRows) {    //
      let id = row.id;
      this.tableData && this.tableData.forEach((e, i) => {
        if (e.id == id) {
          this.shopId = id;
          this.shopIdx = i;
          if (e.dishList) {
            this.carousel = e.dishList;
            return false;
          }
          this.dishLoading = true;
          this.getDish(this.shopId, this.shopIdx);
        }
      });
    },
    getDish(id, i) {    //
      api
        .dishList({
          id: id
        })
        .then(res => {
          if (res.code == 0 && res.data.list) {
            let list = common.isNull(res.data.list, "arr");
            this.$set(this.tableData[i], "dishList", list);
            this.expands = [];
            this.expands.push(this.tableData[i].id);
            this.carousel = list;
          }
          this.dishLoading = false;
        })
        .catch(err => {
          this.dishLoading = false;
        });
    },
    deleteShop() {    //
      this.delLoading = true;
      api
        .deleteBusinesses({ ids: this.id })
        .then(res => {
          if (res.code == 0) {
            this.deleteVisible = false;
            this.loading = true;
            this.shopList('', true);
          } else {
            this.$message.error("");
          }
          this.delLoading = false;
        })
        .catch(err => {
          this.$message.error("");
          this.delLoading = false;
        });
    },
    delGoods() {      //
      this.delGoodsLoading = true;
      api
        .deleteGoods({ id: this.goodsId })
        .then(res => {
          if (res.code == 0) {
            this.$message.success("");
            this.delGoodsVisible = false;
            this.getDish(this.shopId, this.shopIdx);
          }
          this.delGoodsLoading = false;
        })
        .catch(err => {
          this.delGoodsLoading = false;
        });
    },
    updateShop() {     //
      let shopObj = this.shopObj;
      if(!shopObj.shopName) return this.$message.warning("");
      if(!shopObj.mobile) return this.$message.warning("");
      if(!shopObj.address) return this.$message.warning("");
      // if(!shopObj.adminMobile) return this.$message.warning("");
      if(shopObj.shopOwnerMap.length <= 0) return this.$message.warning("");

      this.shopInfoLoading = true;
      let params = {
        id: shopObj.id,
        address: shopObj.address,
        adminMobile: shopObj.adminMobile,
        mobile: shopObj.mobile,
        shopName: shopObj.shopName
      };
      api
        .updateShopInfo(params)
        .then(res => {
          if (res.code == 0) {
            this.shopList('', false);
            this.editVisible = false;
            this.$message.success(res.message);
          } else {
            this.$message.error("");
          }
          this.shopInfoLoading = false;
        })
        .catch(err => {
          this.shopInfoLoading = false;
          this.$message.error("");
        });
    },
    updateGoods() {     //
      let obj = this.goodsObj;
      if(!obj.skuName) return this.$message.warning('');
      if(!obj.sellPrice) return this.$message.warning('');
      if(!obj.skuPic) return this.$message.warning('');
      if(!obj.contentText) return this.$message.warning('');
      this.goodsInfoLoading = true;
      if(obj.contentText) obj.content = "<p>" + obj.contentText + "</p>";
      obj.contentImgs && obj.contentImgs.forEach((e, i) => {
        obj.content += '<img src="' + e + '" />';
      });
      api
        .updateGoods({
          id: obj.id,
          skuName: obj.skuName,
          sellPrice: obj.sellPrice,
          skuPic: obj.skuPic,
          smallPicUrl: obj.smallPicUrl,
          spuId: obj.spuId,
          content: obj.content,
          goodsSkuPics: obj.goodsSkuPics
        })
        .then(res => {
          if (res.code == 0) {
            this.getDish(this.shopId, this.shopIdx);
            this.goodsVisible = false;
          }
          this.goodsInfoLoading = false;
        })
        .catch(err => {
          this.goodsInfoLoading = false;
        });
    },
    addCashier() {     //
      if (!this.addCachierTel) return this.$message.error("");
      this.addCashierLoading = true;
      api
        .updateClerk({
          type: 2,
          mobile: this.addCachierTel,
          shopId: this.shopObj.id
        })
        .then(res => {
          if (res.code == 0) {
            this.shopObj.shopCashier.push({id: res.data, cashierName: this.addCachierTel});
            this.addCachierTel = "";
            this.$message.success("");
            this.shopList('', false);
            this.addCashierVisible = false;
          } else {
            this.$message.error(res.message || "");
          }
          this.addCashierLoading = false;
        })
        .catch(err => {
          this.addCashierLoading = false;
        });
    },
    deleteCashier(index) {  //
      this.delCashierLoading = true;
      api
        .updateClerk({
          type: 1,
          id: this.cashierId
        })
        .then(res => {
          if (res.code == 0) {
            this.$message.success(res.message);
            this.shopList('', false);
            this.delCashierVisible = false;
            this.shopObj.shopCashier && this.shopObj.shopCashier.forEach((e, i) => {
              if(e.id == this.cashierId) {
                this.shopObj.shopCashier.splice(i, 1);
              }
            })
          } else {
            this.$message.error(res.message || '');
          }
          this.delCashierLoading = false;
        })
        .catch(err => {
          this.delCashierLoading = false;
        });
    },
    delGoodsImg(data) {      //
      this.shopInfoLoading = true;
      api
        .updateShopOwnerMap({
          type: 1,
          id: data.id
        })
        .then(res => {
          if(res.code == 0) {
            this.$message.success("");
            this.shopObj.shopOwnerMap && this.shopObj.shopOwnerMap.forEach((e, i) => {
              if (e.id == data.id) {
                this.shopObj.shopOwnerMap.splice(i, 1);
                return false;
              }
            });
          } else{
            this.$message.error("");
          }
          this.shopInfoLoading = false;
        })
        .catch(err => {
          this.shopInfoLoading = false;
        });
    },
    successUpload(res, file, fileList) {       //
      let data = res.data;
      if (res.code == 0 && data.picUrl) this.addShopImg(data);
    },
    addShopImg(data) {     //
      this.shopInfoLoading = true;
      api
        .updateShopOwnerMap({
          type: 2,
          id: this.shopObj.id,
          attachementId: data.id
        })
        .then(res => {
          if (res.code == 0) {
            this.$message.success("");
            this.shopObj.shopOwnerMap.push({
              id: data.picUrl,
              picUrl: data.picUrl
            });
          } else {
            
            this.$message.error("");
          }
          this.shopInfoLoading = false;
        })
        .catch(err => {
          this.shopInfoLoading = false;
        });
    },
    mainImgBefore() {    //
      this.goodsInfoLoading = true;
    },
    goodsImgUpload(res, file, fileList) {     //
      let data = res.data;
      if (res.code == 0 && data.picUrl) this.goodsObj.goodsSkuPicsList.push({
        id: data.id,
        picUrl: data.picUrl,
        smallPicUrl: data.smallPicUrl
      });
      this.goodsObj.goodsSkuPics += "," + data.id;
      this.goodsInfoLoading = false;
    },
    delGoodsSkuPic(i, id) {   //
      this.goodsObj.goodsSkuPicsList.splice(i, 1);
      let picsArr = this.goodsObj.goodsSkuPics.split(',');
      picsArr && picsArr.forEach((e, i) => {
        if(id == e) {
          picsArr.splice(i, 1);
        }
      })
      this.goodsObj.goodsSkuPics = picsArr.join(',');
    },
    mainImgUpload(res, file, fileList) {        //
      let data = res.data;
      this.goodsObj.skuPic = data.picUrl;
      this.goodsObj.smallPicUrl = data.smallPicUrl;
      this.goodsInfoLoading = false;
    },
    contentPicUpload(res, file, fileList) {    //
      let data = res.data;
      if (res.code == 0 && data.picUrl)
        this.goodsObj.contentImgs.push(data.picUrl);
      this.goodsInfoLoading = false;
    },
    delContentImgs(item) {    //
      this.goodsObj.contentImgs && this.goodsObj.contentImgs.forEach((e, i) => {
        if (e == item) {
          this.goodsObj.contentImgs.splice(i, 1);
          return;
        }
      });
    },
    shopInfo(i) {    //
      this.shopIdx = i;
      for (let key in this.shopObj) {
        this.shopObj[key] = this.tableData[this.shopIdx][key];
      }
      this.editVisible = true;
    },
    goodsDetail(id, index) {     //
      for (let key in this.goodsObj) {
        if (this.carousel[index][key])
          this.goodsObj[key] = this.carousel[index][key];
        if (key == "content" && this.goodsObj.content) {
          let obj = this.switchCont(this.goodsObj.content);
          this.goodsObj.contentText = obj.txt;
          this.goodsObj.contentImgs = obj.imgs;
        }
      }
      this.goodsVisible = true;
    },
    deleteBtn(id) {     //
      if (!id) return this.$message.warning("");
      this.id = id;
      this.deleteVisible = true;
    },
    delGoodsDialog(id, index) {   //
      this.delGoodsVisible = true;
      this.goodsId = id;
    },
    deleteCashierDialog(id) {     //
      this.delCashierVisible = true;
      this.cashierId = id;
    },
    selectChange(selection) {    //
      let ids = "";
      selection && selection.forEach((e, i) => {
          ids += e.id + ",";
        });
      ids = ids.substring(0, ids.length - 1);
      this.ids = ids;
    },
    pickTime(e) {    //
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.loading = true;
      this.shopList("reset", true);
    },
    empty() {     //
      this.loading = true;
      this.filters.name = "";
      this.shopList("reset", true);
    },
    changePage(val) {     //
      this.loading = true;
      this.page = val;
      this.shopList('', true);
    },
    sortByOrderT(obj1, obj2) {     //
      let str1 = obj1.orderToday;
      let str2 = obj2.orderToday;
      return str1 - str2;
    },
    sortByOrderH(obj1, obj2) {     //
      let str1 = obj1.orderHistory;
      let str2 = obj2.orderHistory;
      return str1 - str2;
    },
    operateList(arr) {      //
      arr && arr.forEach((e, i) => {
        e.settlingTime = e.settlingTime ? e.settlingTime.replace(".0", "") : '';
      });
      return arr;
    },
    switchCont(str) {         //
      var imgReg = /<img.*?(?:>|\/>)/gi,    //
        srcReg = /[img|IMG].*?src=['|"](.*?(?:[.gif|.jpg]))['|"].*?[/]?>/,    //
        txtReg = "(?<=>)(.|\n)*?(?=</p>)",        //p
        arr = [],
        imgs = [],
        txt = "";
      arr = str.match(imgReg);
      arr && arr.forEach((e, i) => {
        imgs.push(e.match(srcReg)[1]);
      });
      txt = str.match(txtReg)[0];
      return { txt, imgs };
    },
    /*  */
    thisLocation() {
      this.map.plugin("AMap.Geolocation", () => {
        let geolocation = new AMap.Geolocation({
          // true
          enableHighAccuracy: true,
          // 
          timeout: 10000,
          // Pixel(10, 20)
          buttonOffset: new AMap.Pixel(10, 20),
          //  false
          zoomToAccuracy: false,
          //  ,  RB
          buttonPosition: "RB"
        });
        this.map.addControl(geolocation);
        geolocation.getCurrentPosition();
        AMap.event.addListener(geolocation, "complete", data => {
          this.citycode = data.addressComponent.cityCode;
          this.thisPosition = data.position;
          this.thisPosition.message = data.formattedAddress;
          this.chosePosition = this.thisPosition;
          /*  */
          this.cancelLocation();
          this.addCircle();
          /*  */
          this.positionPicker();
        });
        AMap.event.addListener(geolocation, "error", function(data) {
          alert("");
        });
      });
    },
    getMap() {
      this.events = {
        init: (o) => {
          let center = o.getCenter();
          this.center = [center.lng, center.lat];
          this.position = this.center;
          o.getCity(result => {
            this.address = result.province + result.city + result.district;
          })
        },
        click(eventName, handler, context) {
          this.position = [eventName.lnglat.lng, eventName.lnglat.lat];
          console.log(this.position)
        }
      };
    }
  },
  mounted() {
    this.areaOption();
    this.shopList('reset', true);
    this.token = "Bearer " + localStorage.token;
  }
};
</script>

<style scoped lang="scss">
.el-table .el-button {
  color: #5769f0;
  border: 0;
}
.selectBox {
  display: inline-block;
  margin-left: 20px;
  color: #666666;
}
.citySelect .el-select {
  width: 130px;
}
.operationBox {
  display: flex;
  justify-content: space-between;
}
.citySelect .el-select {
  margin-left: 20px;
}
.dishBox {
  background-color: #fff;
  margin-top: 20px;
  padding: 20px 30px;
  box-sizing: border-box;
  .dishBox_tit {
    color: #000000;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 30px;
  }
  .dishList {
    overflow: hidden;
    .dishList_inner {
      display: flex;
      overflow-x: auto;
      overflow-y: hidden;
      flex-wrap: nowrap;
      max-width: 10000px;
      .dishItem {
        width: 300px;
        margin-right: 10px;
        flex-shrink: 0;
        border-radius: 4px;
        overflow: hidden;
        .dishItem_cont {
          height: 200px;
          width: 100%;
          position: relative;
          img {
            width: 100%;
            height: 100%;
          }
          .cont_info {
            width: 100%;
            height: 40px;
            line-height: 40px;
            position: absolute;
            bottom: 0;
            left: 0;
            padding: 0 20px;
            display: flex;
            justify-content: space-between;
            color: #fff;
            background-color: #000000;
            opacity: 0.5;
          }
        }
        .dishItem_oper {
          height: 40px;
          line-height: 40px;
          width: 100%;
          display: flex;
          flex-direction: row;
          background-color: #f1f2f8;
          cursor: pointer;
          .oper_edit {
            flex: 1;
            text-align: center;
            border-right: 1px solid #fff;
            box-sizing: border-box;
          }
          .oper_dele {
            flex: 1;
            text-align: center;
            border-left: 1px solid #fff;
            box-sizing: border-box;
          }
        }
      }
    }
  }
}
.el-table__expanded-cell {
  padding-left: 60px !important;
  .el-form-item {
    display: inline-block;
    width: 200px;
    text-align: center;
    .el-form-item__label {
      width: 200px;
      text-align: center;
      padding: 0;
    }
    .el-form-item__content {
      clear: both;
      text-align: center;
    }
  }
}
.el-button--small {
  font-size: 14px;
}
.dialog_content {
  max-height: 600px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 50px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 50px;
    background: rgba(0, 0, 0, 0.1);
  }
  .el-form-item {
    margin-top: 30px;
    font-size: 16px;
    &:first-child {
      margin-top: 0;
    }
    .el-textarea {
      width: 400px;
      height: 46px;
      textarea {
        box-sizing: border-box;
        height: 46px;
      }
    }
    .dia_item_Img {
      width: 170px;
      height: 170px;
      margin: 0 18px 18px 0;
      display: inline-block;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        &:last-child {
          margin-right: 0;
        }
      }
      i {
        position: absolute;
        top: -13px;
        right: -13px;
        font-size: 26px;
      }
    }
    .cashierList {
      display: flex;
      flex-wrap: wrap;
      .cashierItem {
        position: relative;
        padding: 0 20px;
        margin: 0 20px 20px 0;
        border-radius: 4px;
        border: 1px solid #e8e8e8;
        color: #666666;
        cursor: pointer;
        i {
          position: absolute;
          top: -13px;
          right: -13px;
          font-size: 26px;
        }
        &.active {
          border: 1px solid #5769f0;
          color: #5769f0;
        }
      }
      .addIcon {
        font-size: 40px;
        color: #5769f0;
        cursor: pointer;
      }
    }
  }
  .shopMainImg {
    width: 170px;
    height: 170px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
    }
    .imgUpload {
      position: absolute;
      top: 10px;
      left: 10px;
      width: 100px;
      height: 100px;
      opacity: 0.6;
    }
  }
}
.table-expand {
  .expandSkuName {
    width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
</style>


