<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="dateArray"
            unlink-panels
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            @change="pickTime"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                class="searchInput"
                v-model="filters.name"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTablelist"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <el-button type="primary" plain @click="batchprocessing(false)"
          ></el-button
        >
        <el-button type="primary" plain @click="batchprocessing(true)"
          ></el-button
        >
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%; margin-top:20px;"
          :default-sort="{ prop: 'date', order: 'descending' }"
          @selection-change="selectItems"
        >
          <el-table-column
            type="selection"
            width="60"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="id"
            label="ID"
            width="70"
            align="center"
            sortable
          ></el-table-column>
          <el-table-column
            prop="shopName"
            label=""
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="skuName"
            label=""
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column
            prop="skuType"
            label=""
            width="100"
            align="center"
          ></el-table-column>
          <el-table-column prop="marketPrice" label="" align="center">
            <template slot-scope="scope">
              <p v-show="scope.row.sellPrice">
                <span>:{{ scope.row.sellPrice }}</span>
              </p>
              <p v-show="scope.row.singlyPrice">
                <span>:{{ scope.row.singlyPrice }}</span>
              </p>
              <p v-show="scope.row.spikePrice">
                <span>:{{ scope.row.spikePrice }}</span>
              </p>
              <p v-show="scope.row.actPrice">
                <span>:{{ scope.row.actPrice }}</span>
              </p>
            </template></el-table-column
          >
          <el-table-column
            prop="updateTime"
            label=""
            width="170"
            align="center"
            sortable
          ></el-table-column>
          <el-table-column
            prop="skuPic"
            label=""
            width="160"
            align="center"
          >
            <template slot-scope="scope">
              <img
                @click="selectimg(scope.row.skuPic)"
                class="goodsImg"
                :src="scope.row.skuPic"
              />
            </template>
          </el-table-column>
          <el-table-column
            prop="content"
            label=""
            align="center"
            show-overflow-tooltip
          >
            <template slot-scope="scope">
              <div class="goodsContent" v-html="scope.row.content"></div>
            </template>
          </el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="operating(false, scope.row.id)"
                ></el-button
              >
              <el-button
                type="text"
                size="small"
                @click="operating(true, scope.row.id)"
                ></el-button
              >
              <!-- <el-button type="text" size="small"></el-button> -->
              <el-button type="text" size="small" @click="showDetail(scope.row)"
                ></el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{ total }}{{ pages }}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <el-dialog :visible.sync="dialogVisibleImgPic" :append-to-body="true">
      <img width="100%" :src="previewImg" alt />
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="detailVisible" width="50%">
      <el-dialog :visible.sync="dialogVisibleImg" :append-to-body="true">
        <img width="100%" :src="goodsContent.skuPic" alt />
      </el-dialog>
      <div class="dialog_content">
        <div class="dialog_item" v-for="(item, key) in titledatas" :key="key">
          <div class="dia_item_l">{{ item }}</div>
          <div v-if="key == 'skuPic'" class="dia_item_Img">
            <img
              :src="goodsContent[key]"
              @click="dialogVisibleImg = true"
              alt
            />
          </div>
          <div v-else-if="key == 'goodsSkuPicsList'" class="dia_item_Imgs">
            <img
              v-for="item in goodsContent[key]"
              :key="item"
              :src="item"
              alt
            />
          </div>
          <div
            v-else-if="key == 'content'"
            class="dia_item_r"
            v-html="goodsContent[key]"
          ></div>
          <div v-else class="dia_item_r">{{ goodsContent[key] }}</div>
        </div>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      v-loading="loadingReject"
      :close-on-click-modal="false"
      @close="rejectClose"
      title=""
      :visible.sync="dialogFail"
      width="680px"
    >
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="">
          <el-select v-model="formReject.select" placeholder="">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select == 0">
          <el-input
            type="textarea"
            placeholder=""
            v-model="formReject.textarea"
            :maxlength="formReject.maxlength"
            @input="descInput"
            :rows="3"
            style="width:402px;"
            show-word-limit
          >
          </el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            :on-exceed="limitError"
            :limit="5"
            ref="rejectUpload"
            action="#"
            list-type="picture-card"
            :on-success="imgSuccess"
            :http-request="beforeImgUploadHome"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible" :modal='false'>
            <img width="100%" :src="dialogImageUrl" alt="" />
          </el-dialog>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="formReject.isSend" label="1"></el-radio>
          <el-radio v-model="formReject.isSend" label="0"></el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="formRejectCancle"> </el-button>
        <el-button
          type="primary"
          :loading="uploadLoading"
          @click="formRejectSubmit"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template> 
<script>
import api from "../../../../assets/js/api";
import common from "../../../../assets/js/common.js";
export default {
  name: "newStock",
  data() {
    return {
      formReject: {
        select: null,
        option: [
          {
            value: "",
            label: ""
          },
          {
            value: "",
            label: ""
          },
          {
            value: 0,
            label: ""
          }
        ],
        textarea: "",
        isSend: "1",
        maxlength: "30",
        remnant: 0
      },
      dateArray: null,
      timeEnd: "",
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [],
      goodsContent: [],
      detailVisible: false,
      dialogFail: false,
      dialogDel: false,
      page: 1, //
      pages: 0,
      total: 0,
      rows: 10,
      aduitItemData: null,
      beginTime: null,
      endTime: null,
      aduitDatas: [],
      dialogVisible: false,
      dialogImageUrl: null,
      dialogImageArr: [],
      titledatas: {
        shopName: "",
        shopType: "",
        skuName: "",
        skuType: "",
        actPrice: "",
        sellPrice: "",
        singlyPrice: "",
        spikePrice: "",
        skuPic: "",
        content: "",
        goodsSkuPicsList: ""
      },
      loadingReject: false,
      loadingTable: false,
      dialogVisibleImg: false, //
      dialogVisibleImgPic: false, //
      previewImg: null, //
      uploadLoading: false
    };
  },
  methods: {
    selectimg(img) {
      //
      this.dialogVisibleImgPic = true;
      this.previewImg = img;
    },
    limitError() {
      //
      this.$message({
        type: "error",
        message: ""
      });
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getTablelist();
    },
    handleClose2(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    pickTime(e) {
      //
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.getTablelist();
    },
    getTablelist() {
      //
      this.loadingTable = true;
      api
        .goodsSkuList({
          page: this.page,
          rows: this.rows,
          startTime: this.beginTime,
          endTime: this.endTime,
          skuName: this.filters.name
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data && data.list) {
            this.pages = data.pages;
            this.total = data.total;
            this.tableData = data.list;
            this.loadingTable = false;
          }
        })
        .catch(err => {});
    },
    operating(status, id) {
      //
      this.aduitDatas = [];
      this.aduitDatas.push(id);
      if (status) {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          this.aduitItem([id], true);
          this.$message({
            type: "success",
            message: ""
          });
        });
      } else {
        this.dialogFail = true;
      }
    },
    batchprocessing(status) {
      if (status) {
        //
        let msg = status
          ? ",?"
          : ",?";
        this.$confirm(msg, "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(() => {
          this.aduitItem(this.aduitDatas, status);
        });
      } else {
        this.dialogFail = true;
      }
    },
    aduitItem(id, flag, result, pivcs, smsIsSend) {
      //
      //ids  
      //aduitStatus  "2"
      //aduitItemrejectResult 
      //rejectPics 
      //smsIsSend   0   1 
      api
        .goodsSkuListAudit({
          ids: id,
          aduitStatus: flag ? "2" : "3",
          rejectResult: result,
          rejectPics: pivcs,
          smsIsSend: smsIsSend
        })
        .then(res => {
          let data = res.data;
          if (res.code == 0 && data) {
            this.getTablelist();
            this.dialogFail = false;
            this.aduitDatas = [];
            this.loadingReject = false;
          }
        })
        .catch(err => {});
    },
    selectItems(selection) {
      //
      this.aduitDatas = [];
      if (selection.length > 0) {
        for (let i = 0; i < selection.length; i++) {
          this.aduitDatas.push(selection[i].id);
        }
      }
    },
    showDetail(data) {
      //
      this.goodsContent = data;
      this.detailVisible = true;
    },
    formRejectSubmit() {
      //
      if (
        this.formReject.select == null ||
        (this.formReject.select == "0" && !this.formReject.textarea)
      ) {
        return this.$message.warning("");
      }
      let urlArr = [];
      this.loadingReject = true;
      this.dialogImageArr.forEach((value, i) => {
        urlArr.push(value.imgUrl);
      });
      let imgUrlStr = urlArr.join(",");
      this.aduitItem(
        this.aduitDatas,
        false,
        this.formReject.select || this.formReject.textarea,
        imgUrlStr,
        this.formReject.isSend
      );
    },
    rejectClose() {
      //
      // 
      this.$set(this.formReject, "select", null);
      this.$set(this.formReject, "textarea", null);
      this.$set(this.formReject, "isSend", "1");
      this.$set(this.formReject, "remnant", 0);
      this.dialogImageUrl = null;
      (this.dialogImageArr = []), this.$refs.rejectUpload.clearFiles();
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      // this.dialogVisible = true;
    },
    //
    empty() {
      this.filters.name = "";
      this.getTablelist();
    },
    descInput() {
      //
      this.formReject.remnant = this.formReject.textarea.length;
    },
    imgSuccess() {},
    beforeImgUploadHome(param) {
      let that = this;
      this.uploadLoading = true;
      uploadImg(param.file).then(imgUrl => {
        that.dialogImageUrl = imgUrl;
        param.file.imgUrl = imgUrl;
        that.dialogImageArr.push(param.file);
        this.uploadLoading = false;
      });
      return false;
    },
    handleRemove(file, fileList) {
      this.dialogImageArr = this.dialogImageArr.filter(ele => {
        ele.uid == file.uid;
      });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    formRejectCancle() {
      //
      this.dialogFail = false;
      this.formReject = {
        select: null,
        option: [
          {
            value: 1,
            label: ""
          },
          {
            value: 2,
            label: ""
          },
          {
            value: 0,
            label: ""
          }
        ],
        textarea: null,
        isSend: "1",
        maxlength: 30,
        remnant: 0
      };
    }
  },
  mounted() {
    this.getTablelist();
  }
};
var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    // let that = _this;
    form.append("file", file);
    api
      .upload(form)
      .then(res => {
        return resolve(res.data.picUrl);
      })
      .catch(err => {
        console.log(err);
      });
  });
};
</script>

<style scope="scoped" lang="scss">
.goodsImg {
  width: 60px;
  height: 60px;
}
.searchInput {
  input {
    width: 250px;
  }
}
.goodsContent {
  display: flex;
  flex-direction: row;
  align-items: center;
  max-height: 60px;
  img {
    max-width: 60px;
    max-height: 60px;
  }
}

.goodsContent div {
  text-align: left;
  margin-left: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.el-dialog__header {
  height: 60px;
  line-height: 60px;
  padding: 0 20px;
  .el-dialog__title {
    font-size: 18px;
  }
  .el-dialog__headerbtn {
    font-size: 18px;
    top: 16px;
  }
}

.dialog_content {
  max-height: 600px;
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  &::-webkit-scrollbar-thumb {
    border-radius: 50px;
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    background: rgba(0, 0, 0, 0.2);
  }
  &::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    border-radius: 50px;
    background: rgba(0, 0, 0, 0.1);
  }
  .dialog_item {
    display: flex;
    flex-direction: row;
    padding-top: 30px;
    &:first-child {
      padding-top: 0;
    }
    .dia_item_l {
      width: 140px;
      text-align: right;
      color: #333333;
    }
    .dia_item_r {
      color: #666666;
      width: 760px;
    }
    .dia_item_Img {
      width: 760px;
      img {
        width: 170px;
        height: 170px;
        margin: 0 18px 18px 0;
        &:last-child {
          margin-right: 0;
        }
      }
    }
    .dia_item_Imgs {
      width: 760px;
      img {
        width: 170px;
        height: 170px;
        margin: 0 18px 18px 0;
        &:last-child {
          margin-right: 0;
        }
      }
    }
  }
}
</style>


