<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">           
          <el-date-picker
            v-model="dateArray"
            @change="pickTime"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span></span>
            <el-select v-model="applyStatus" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true">
            <el-form-item>
              <el-input v-model="keywords" @keyup.enter.native="search" placeholder="" style="width: 260px;"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0"> 
        <el-button type="primary" plain @click="operateFunc('multi', 1)"></el-button>
        <el-button type="primary" plain @click="operateFunc('multi', 2)"></el-button>
        <el-table
          v-loading="tableLoading"
          :data="tableData"
          style="width: 100%; margin-top:20px"
          :default-sort="{prop: 'date', order: 'descending'}"
          @selection-change="selectChange"
        >
          <el-table-column type="selection" width="55" fixed="left" :selectable="selectable"></el-table-column>
          <el-table-column prop="orderCode" label="" min-width="200"></el-table-column>
          <el-table-column prop="createTime" label="" width="200" sortable></el-table-column>
          <el-table-column prop="mobile" label="" width="160"></el-table-column>
          <el-table-column prop="invoiceAmount" label="" width="120" sortable></el-table-column>
          <el-table-column prop="billType" label="" width="120"></el-table-column>
          <el-table-column prop="invoiceOpen" label="" width="120"></el-table-column>
          <el-table-column prop="dutyParagraph" label="" width="180"></el-table-column>
          <el-table-column prop="detailAddress" label="" width="180"></el-table-column>
          <el-table-column prop="chatName" label="" width="100"></el-table-column>
          <el-table-column prop="addressMobile" label="" width="180"></el-table-column>
          <el-table-column prop="email" label="" width="180"></el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <div class="table-scope">                 
                <span v-if="scope.row.applyStatus==2" class="green"></span>
                <span v-if="scope.row.applyStatus==3" class="red"></span>
                <span v-if="scope.row.applyStatus==1" class="grgy"></span>
              </div>
            </template>      
          </el-table-column>
          <el-table-column label="" width="120" fixed="right">
            <template slot-scope="scope"> 
              <span v-if="scope.row.applyStatus==1">
                <el-button type="text" @click="operateFunc('single', 1, scope.row.id)"></el-button>
                <el-button type="text" @click="operateFunc('single', 2, scope.row.id)"></el-button>
              </span>
              <span v-else>
                <el-button type="text" disabled></el-button>
                <el-button type="text" disabled></el-button>
              </span>
            </template> 
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{total}}{{pages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              :total="total"
              layout="prev, pager, next, jumper"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-else class="table-default" v-loading="tableLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p></p>
      </div>
    </div>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogPass" width="540px" v-loading="passLoading">
      <div class="dialog-info"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPass = false"> </el-button>
        <el-button type="primary" @click="pass"> </el-button>
      </span>
    </el-dialog>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogForbidden" width="900px" v-loading="rejectLoading">
      <div class="dialogForbidden-info">
        <div class="rejectArea">
          <span class="rejectArea_tit"></span>
          <el-select v-if="!isCustom" v-model="rejectSel" placeholder="">
            <el-option
              v-for="item in rejectOpt"
              :key="item.id"
              :label="item.value"
              :value="item.id"
            ></el-option>
          </el-select>
          <el-input 
            v-if="isCustom"
            type="textarea"
            :autosize="true"
            placeholder=""
            v-model="textarea"
            maxlength="100"
          >
          </el-input>
        </div>
        <div class="rejectMessage">
          <span></span>
          <template>
            <el-radio v-model="radio" label="1"></el-radio>
            <el-radio v-model="radio" label="2"></el-radio>
          </template>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogForbidden = false"> </el-button>
        <el-button type="primary" @click="reject"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      id: '',
      ids: '',
      checkType: '',
      dialogPass: false,
      dialogForbidden: false,
      beginTime: "",
      endTime: "",
      keywords: "",
      applyStatus: "",
      rows: 10,
      page: 1,
      pages: 1,
      total: 1,
      rejectSel: "",
      isCustom: false,
      textarea: "",
      radio: "1",
      tableLoading: true,
      passLoading: false,
      rejectLoading: false,
      options: [       //
        {
          id: '',
          value: ""
        },
        {
          id: 1,
          value: ""
        },
        {
          id: 2,
          value: ""
        },
        {
          id: 3,
          value: ""
        }
      ],
      rejectOpt: [],
      dateArray: "",
      tableData: []
    };
  },
  watch: {
    applyStatus(val) {    //
      this.tableLoading = true;
      this.page = 1;
      this.billList();
    },
    rejectSel(val) {
      this.isCustom = val == 'custom' ? true : false;
    }
  },
  methods: {
    selectable(row, index) {
      return row.applyStatus == 1 ? true : false;
    },
    pickTime(e) {
      if(e == null) {
        this.beginTime = '';
        this.endTime = '';
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.tableLoading = true;
      this.page = 1;
      this.billList();
    },
    billList() {    //
      api.showList({
        beginTime: this.beginTime,
        endTime: this.endTime,
        keywords: this.keywords,
        applyStatus: this.applyStatus,
        page: this.page,
        rows: this.rows        
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.tableData = data.list ? this.operateList(data.list) : [];
          this.pages = data.pages;
          this.total = data.total;
          this.tableLoading = false;
        }
      }).catch(err => {});
    },
    operateList(arr) {
      arr.forEach((e, i) => {
        let str1 = '', str2 = '';
        if(e.initialType == 1) str1 = '';
        if(e.initialType == 2) str1 = '';
        if(e.sourceType == 1) str2 = '';
        if(e.sourceType == 2) str2 = '';
        e.billType = str1 + str2;
      });
      return arr;
    },
    rejectReason() {    //
      api.rejectReason().then(res => {
        if(res.code == 0 && res.data) {
          let data = res.data;
          data.forEach((e, i) => {
            this.rejectOpt.push({
              id: i,
              value: e.reason
            });
          });
          this.rejectOpt.push({
            id: 'custom',
            value: ""
          })
        }
      }).catch(err => {});
    },
    search() {      //
      this.tableLoading = true;
      this.page = 1;
      this.billList();
    },
    //
    empty(){
      this.tableLoading = true;
      this.keywords = "";
      this.page = 1;
      this.billList();
    },
    operateFunc(checkType, type, id) {      //
      if(checkType == 'multi' && this.ids == '') return this.$message.warning('');
      id && (this.id = id);
      this.checkType = checkType;
      if(type == 1) return this.dialogPass = true;
      if(type == 2) {
        this.rejectSel = "";
        this.textarea = "";
        this.isCustom = false; 
        this.dialogForbidden = true;
      }
    },
    pass() {        //
      this.passLoading = true;
      let ids = this.checkType == 'multi' ? this.ids : this.id;
      api.agreeApply({
        ids: ids,
        idsLength: ids.toString().split(',').length,
        applyStatus: 2
      }).then(res => {
        if(res.code == 1) {
          this.dialogPass = false;
          this.passLoading = false;
          this.$message.success('');
          this.tableLoading = true;
          this.billList();
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {})
    },
    reject() {      //
      let content = '';
      this.rejectOpt.forEach((e, i) => {
        if(e.id == this.rejectSel) {
          this.content = e.id == 'custom' ? this.textarea : e.value;
        }
      })
      if(this.content == '') {
        this.$message.error('');
        return false;
      };
      this.rejectLoading = true;
      let ids = this.checkType == 'multi' ? this.ids : this.id;
      api.agreeApply({
        ids: ids,
        idsLength: ids.toString().split(',').length,
        applyStatus: 3,
        isSend: this.radio,
        content: this.content
      }).then(res => {
        if(res.code == 1) {
          this.dialogForbidden = false;
          this.rejectLoading = false;
          this.$message.success('');
          this.tableLoading = true;
          this.billList();
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {})
    },
    selectChange(selection) {      //
      let ids = '';
      selection && selection.forEach((e, i) => {
        ids += e.id + ',';
      });
      ids = ids.substring(0 ,ids.length - 1);
      this.ids = ids;
    },
    listPage(val) {       //
      this.tableLoading = true;
      this.page = val;
      this.billList();
    }
  },
  mounted() {
    this.billList();
    this.rejectReason();
  }
};
</script>

<style scoped lang="scss">
.dialogForbidden-info {
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  margin: 30px 0;
  .rejectArea {
    display: flex;
    .rejectArea_tit {
      margin-right: 20px;
    }
    .el-textarea {
      width: 600px;
    }
  }
  .rejectMessage {
    margin-top: 30px;
  }
}
</style>



