//http
import http from './http.js';
import QS from 'qs';
const get = http.get;
const post = http.post;
let api = {
    login: p => post('app/auth/loginByPassword' + p),       //
    findShopAccountTotal: p => post('manage/shopAccount/findShopAccountTotal', p),    //
    refreshShopAccountTotal: p => post('manage/shopAccount/refreshShopAccountTotal', p),    //
    searchShopWithdrawList: p => post('manage/shopAccount/searchShopWithdrawList', p),  //
    approvePass: p => post('manage/shopAccount/approvePass', p),     //
    approveReject: p => post('manage/shopAccount/approveReject', p),    //
    findShopAccountDetailById: p => post('manage/shopAccount/findShopAccountDetailById', p),   //
    findAccountLog: p => post('manage/shopAccount/findAccountLog', p),     //
    searchUserAdvanceList: p => post('manage/userAdvance/searchUserAdvanceList', p),    //
    batchApplyPass: p => post('manage/userAdvance/batchApplyPass', p),    //()
    batchApplyReject: p => post('manage/userAdvance/batchApplyReject', p),   //()
    agreeRefund: p => post('manage/payRefund/agreeRefund', p),   //
    getList: p => post('manage/payRefund/getList', p),   //
    getTableList: p => post('manage/payRefund/getTableList', p),   //
    refuseRefund: p => post('manage/payRefund/refuseRefund', p),   //
    exportRep: p => get('manage/payRefund/export', p),   //
    upload: p => post('manage/img/upload', p),   //
    selectGoodsList: p => get('manage/actGoodsSku/selectGoodsList', p),   //
    list: p => get('manage/shop/list', p),   //
    findActivityRecommend: p => post('manage/activity/findActivityRecommend', p),   //
    saveActivityRecommend: p => post('manage/activity/saveActivityRecommend', p),   //
    goodsSkuList: p => post('manage/goodsSku/pageListForAudit',p),//
    goodsSkuListAudit: p => post('manage/goodsSku/auditGoodsSku', p),//  /
    storetotal: p => post('manage/detailed/getTableList', p),   //
    areaList: p => get('manage/detailed/listAllShopZone', p), //
    storeList: p => post('manage/detailed/getList', p),   //
    getDetails: p => post('manage/detailed/getDetails', p),    //
    getNoDetails: p => post('manage/detailed/getNoDetails', p),    //
    showList: p => post('manage/orderInvoice/showList', p),      //
    agreeApply: p => post('manage/orderInvoice/agreeApply', p),       ///
    rejectReason: p => post('manage/orderInvoice/getReason', p),     //
    paymentList: p => post('manage/paymentType/getList', p),     //
    PlatformList: p => post('manage/Platform/getList', p),            //
    updateBusinesses: p => post('manage/detailed/updateBusinesses', p),   //
    shopList: p => post('manage/BusinessListings/getList', p),     //
    dishList: p => post('manage/BusinessListings/getShopList', p),    //
    orderList: p => post('manage/orderList/getList', p),    // 
    orderTotal: p => post('manage/orderList/getListTotal', p),   // 
    orderEditor: p => post('manage/orderList/updateOrderList', p),     // 
    shopEnterList: p => post('manage/shopEnter/showList', p),     // 
    shopEnterOperation: p => post('manage/shopEnter/operationById', p),     // 
    deleteBusinesses: p => post('manage/BusinessListings/deleteBusinesses', p),      //
    updateShopInfo: p => post('manage/BusinessListings/updateBusinesses', p),      //
    updateClerk: p => post('manage/BusinessListings/updateClerk', p),                //      
    updateGoods: p => post('manage/BusinessListings/updateGoods', p),                //
    updateShopOwnerMap: p => post('manage/BusinessListings/updateShopOwnerMap', p),    //
    deleteGoods: p => post('manage/BusinessListings/deleteGoods', p)                 //
};
export default api;


