<template>
  <div class="c-box">
    <div class="refresh">
      <div class="refreshInner" @click="refresh">
        <span></span>
        <i class="el-icon-refresh"></i>
      </div>
    </div>
    <div class="c-merchant" v-loading="dataLoading">
      <!--  -->
      <ul>
        <li class="income-li" v-for="(item, index) in incomeList" :key="index">
          <p>{{item.title}}</p>
          <h3>
            <b>{{item.number}}</b>
            <span>{{item.sum}}</span>
          </h3>
        </li>
      </ul>
      <!--  -->
      <ul>
        <li class="order-li" v-for="(item, index) in orderList" :key="index">
          <div class="order-img">
            <i class="icon" :class="item.iconImg"></i>
          </div>
          <h3>
            <b>{{item.number}}</b>
            <p>{{item.title}}</p>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <!-- option -->
      <div class="table-title">
        <div class="title-left">
          <el-button type="primary" icon="el-icon-upload2" style="margin-right:28px;" @click="exportExcel"></el-button>
          <el-date-picker
            v-model="dateArray"
            @change="pickTime"
            :default-time="['00:00:00', '23:59:59']"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span></span>
            <el-select v-model="applyStatus" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" style="float:right;">
            <el-form-item>
              <el-input v-model="searchKey" @keyup.enter.native="search" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <!--  -->
      <div class="table-box" v-if="tableData.length>0">
        <el-table
          v-loading="shopLoading"
          :data="tableData"
          style="width: 100%;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column label="ID" width="70" fixed>
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column prop="shopName" label="" width="200"></el-table-column>
          <el-table-column prop="accountName" label="" width="220"></el-table-column>
          <el-table-column prop="bankCardName" label="" width="180"></el-table-column>
          <el-table-column prop="mobile" label="" width="160"></el-table-column>
          <el-table-column prop="createTime" label="" width="200" sortable></el-table-column>
          <el-table-column prop="accountCardholderName" label="" width=""></el-table-column>
          <el-table-column prop="cashAmount" label="" width="120" sortable></el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <div class="table-scope">    
                <template v-if="scope.row.applyStatus==2"> 
                  <span class="green"></span>
                </template>   
                <template v-if="scope.row.applyStatus==3"> 
                  <span class="red"></span>
                </template>   
                <template v-if="scope.row.applyStatus==1"> 
                  <span class="grgy"></span>
                </template>   
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" width="180" fixed="right">
            <template slot-scope="scope">
              <template v-if="scope.row.applyStatus==1">
                <el-button type="text" @click="passDialog(scope.row.cashId)"></el-button>
                <el-button type="text" @click="rejectDialog(scope.row.cashId)"></el-button>
              </template>
              <template v-else>
                <el-button type="text" disabled></el-button>
                <el-button type="text" disabled></el-button>
              </template>              
              <el-button type="text" @click="checkWithdraw(scope.row.cashId, scope.row.cashAmount, scope.row.beginTime, scope.row.endTime)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{total}}{{pages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-else class="table-default" v-loading="shopLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p></p>
      </div>
    </div>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogDetail" width="1056px">
      <div class="dialogDetail-title">
        
        <b>{{amount}}</b>
      </div>
      <div class="dialog-table">
        <el-table v-loading="billLoading" :data="tableDetail" border style="width: 100%">
          <el-table-column prop="orderCode" label="" width="200"></el-table-column>
          <el-table-column prop="userName" label=""></el-table-column>
          <el-table-column prop="serviceAmount" label="" width="100"></el-table-column>
          <el-table-column prop="typeTxt" label=""></el-table-column>
          <el-table-column prop="billTxt" label=""></el-table-column>
          <el-table-column prop="realUnitPrice" label=""></el-table-column>
          <el-table-column prop="realShopUnitPirce" label=""></el-table-column>
          <el-table-column prop="createTime" label="" width="180"></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{dTotal}}{{dPages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="detailPage"
              :current-page.sync="dPage"
              :page-count="dPages"
              layout="prev, pager, next, jumper"
              :total="dTotal"
            ></el-pagination>
          </div>
        </div>
      </div>
      <div class="dialog-table">
        <el-table v-loading="changeLoading" :data="changeList" border style="width: 100%">
          <el-table-column prop="operateAmount" label=""></el-table-column>
          <el-table-column prop="remark" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <a v-if="scope.row.operateType==1"></a>
              <a v-if="scope.row.operateType==2"></a>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label=""></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{cTotal}}{{cPages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="changePage"
              :current-page.sync="cPage"
              :page-count="cPages"
              layout="prev, pager, next, jumper"
              :total="cTotal"
            ></el-pagination>
          </div>
        </div>
      </div>
    </el-dialog>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogPass" width="540px">
      <div class="dialog-info"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogPass = false"> </el-button>
        <el-button type="primary" @click="pass"> </el-button>
      </span>
    </el-dialog>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogFail" width="680px">
      <el-form ref="form" label-width="100px">
        <el-form-item label="">
          <el-input type="textarea" v-model="textarea"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            ref="upload"
            :action="action"
            :headers="{Authorization: token}"
            list-type="picture-card"
            :on-preview="previewImg"
            :on-success="successUpload"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :modal="false" :visible.sync="isImgPreview">
            <img width="100%" :src="previewUrl" alt>
          </el-dialog>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="isSend" label="1"></el-radio>
          <el-radio v-model="isSend" label="0"></el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelBtn"> </el-button>
        <el-button type="primary" @click="reject"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      dialogDetail: false,   //
      dialogPass: false,     //
      dialogFail: false,     //
      isImgPreview: false,
      previewUrl: "",        //url
      isSend: '1',              //radio
      textarea: '',             //
      imgList: [],              //
      startTime: '',    //
      endTime: '',      //
      singleStartTime: '',   //
      singleEndTime: '',     //
      page: 1,           //
      pages: 0,
      total: 0,
      dPage: 1,          //
      dPages: 0,
      dTotal: 0,
      rows: 10,
      cPage: 1,
      cPages: 0,
      cTotal: 0,
      cashId: '',         //id
      amount: 0,           //
      applyStatus: '',  //id
      searchKey: '',
      dataLoading: true,
      shopLoading: true,
      billLoading: true,
      changeLoading: true,
      action: "/api/manage/img/upload",
      dateArray: "",
      token: '',
      options: [       //
        {
          id: '',
          value: ""
        },
        {
          id: 1,
          value: ""
        },
        {
          id: 2,
          value: ""
        },
        {
          id: 3,
          value: ""
        }
      ],
      incomeList: [],    //
      orderList: [],     //
      tableData: [],     //
      tableDetail: [],   //
      changeList: [],    //
    };
  },
  watch: {
    applyStatus(value) {     //
      this.shopLoading = true;
      this.page = 1;
      this.withdrawList();
    }
  },
  methods: {
    getIncome() {    //
      api.findShopAccountTotal({
        userType: 2,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    refresh() {      //
      this.dataLoading = true;
      api.refreshShopAccountTotal({
        userType: 2,
        startTime: this.startTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    incomeCallback(res) {     //
      if(res.code == 0) {
        let data = res.data, incomeList = [], orderList = [];
        incomeList = [
          {
              title: "  |  ",
              number: data.wxPayTotal || 0,
              sum: data.wxPayMoney || 0
          }, {
              title: "  |  ",
              number: data.yuePayTatal || 0,
              sum: data.yuePayMoney || 0
          }, {
              title: "  |  ",
              number: data.wxRefundTatal || 0,
              sum: data.wxRefundTatalMoney || 0
          },
          {
              title: "  |  ",
              number: data.yueRefundTatal || 0,
              sum: data.yueRefundTatalMoney || 0
          }
        ];
        orderList = [
          {
              iconImg: "icon-order1",
              number: data.toDayRefundTatal || 0,
              title: "()"
          },
          {
              iconImg: "icon-money1",
              number: data.toDayRefundTatalMoney || 0,
              title: "()"
          },
          {
              iconImg: "icon-order2",
              number: data.hisRefundTatal || 0,
              title: "()"
          },
          {
              iconImg: "icon-money2",
              number: data.hisRefundTatalMoney || 0,
              title: "()"
          }
        ];
        this.incomeList = incomeList;
        this.orderList = orderList;
        this.dataLoading = false;
      }
    },
    withdrawList() {      //
      // applyStatus     1  2  3 
      api.searchShopWithdrawList({
        shopName: this.searchKey,
        applyStatus: this.applyStatus,
        startTime: this.startTime,
        endTime: this.endTime,
        page: this.page,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.pages = data.pages;
          this.total = data.total;
          this.tableData = data.list ? this.operateList(data.list) : [];
          this.shopLoading = false;
        } 
      }).catch(err => {})
    },
    operateList(arr) {
      arr.forEach((e, i) => {
        e.createTime = e.createTime.replace('.0', '');
      });
      return arr;
    },
    //
    pickTime(e) {
      if(e == null) {
        this.startTime = '';
        this.endTime = '';
      } else {
        this.startTime = e[0];
        this.endTime = e[1];
      }
      this.dataLoading = true;
      this.shopLoading = true;
      this.page = 1;
      this.withdrawList();
      this.getIncome();
    },
    checkWithdraw(id, amount, startTime, endTime) {     //
      this.dialogDetail = true;
      this.cashId = id;
      this.amount = amount;
      this.singleStartTime = startTime;
      this.singleEndTime = endTime;
      this.billLoading = true;
      this.changeLoading = true;
      this.dPage = 1;
      this.cPage = 1;
      this.billList();
      this.changeListFunc();
    },
    billList() {     //
      api.findShopAccountDetailById({
        cashId: this.cashId,
        startTime: this.singleStartTime,
        endTime: this.singleEndTime,
        page: this.dPage,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.dPages = data.pages;
          this.dTotal = data.total;
          this.tableDetail = data.list ? this.flagType(data.list) : [];   //
          this.billLoading = false;
        }
      }).catch(err => {})
    },
    changeListFunc() {   //
      //operateType: 1  2  
      api.findAccountLog({
        cashId: this.cashId,
        startTime: this.singleStartTime,
        endTime: this.singleEndTime,
        page: this.cPage,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.cPages = data.pages;
          this.cTotal = data.total;
          this.changeList = data.list ? data.list : [];
          this.changeLoading = false;
        }
      }).catch(err => {});
    },
    search() {      //
      this.shopLoading = true;
      this.page = 1;
      this.withdrawList();
    },
    //
    empty() {
      this.shopLoading = true;
      this.searchKey = '';
      this.page = 1;
      this.withdrawList();
    },
    exportExcel() {    //
      location.href = '/api/manage/shopAccount/export?shopName='+ this.searchKey +'&applyStatus='+ this.applyStatus +'&startTime='+this.startTime+'&endTime='+this.endTime;
    },
    listPage(val) {    //
      this.shopLoading = true;
      this.page = val;
      this.withdrawList();
    },
    detailPage(val) {    //
      this.billLoading = true;
      this.dpage = val;
      this.billList();
    },
    changePage(val) {    //
      this.changeLoading = true;
      this.cpage = val;
      this.changeListFunc();
    },
    flagType(list) {    //
      // flagType:(1  4  5 6 7)'
      // isBill:'0  1  '
      list.forEach((e, i) => {
        e.typeTxt = (type => {
          if(type == 1) return '';
          if(type == 4) return '';
          if(type == 5) return '';
          if(type == 6) return '';
          if(type == 7) return '';
        })(e.flagType);
        e.billTxt = (isBill => {
          if(isBill == 0) return '';
          if(isBill == 1) return '';
        })(e.isBill);
      })
      return list;
    },
    passDialog(id) {            //
      this.dialogPass = true;
      this.cashId = id;
    },
    pass() {                   //
      api.approvePass({
        id: this.cashId
      }).then(res => {
        if(res.code == 0) {
          this.$message.success('');
          this.dialogPass = false;
          this.withdrawList();
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {

      })
    },
    rejectDialog(id) {          //
      this.dialogFail = true;
      this.cashId = id;
    },
    reject() {                //
      if (!this.textarea) return this.$message.warning('');
      if (this.imgList.length <= 0) return this.$message.warning('');
      let imgUrl = '';
      this.imgList.forEach((e, i) => { imgUrl += e + ','; });
      imgUrl = imgUrl.substring(0, imgUrl.length - 1);
      api.approveReject({
        cashId: this.cashId,
        content: this.textarea,
        imageUrl: imgUrl,
        isSend: this.isSend
      }).then(res => {
        if(res.code == 0) {
          this.$message.success('');
          this.dialogFail = false;
          this.withdrawList();
          this.$refs.upload.clearFiles();
          this.imgList == [];
        } else {
          this.$message.error(res.message);
        }
      }).catch(err => {
        console.log(err)
      })
    },
    cancelBtn() {
      this.dialogFail = false;
    },
    previewImg(file) {
      this.previewUrl = file.url;
      this.isImgPreview = true;
    },
    successUpload(res, file, fileList) {
      let data = res.data;
      if(res.code == 0 && data.picUrl) this.imgList.push(data.picUrl);
    },
    handleRemove(file, fileList) {
      if(fileList.length > 0) this.imgList = [];
      fileList.forEach((e, i) => {
        if(e.response.data.picUrl) this.imgList.push(e.response.data.picUrl);
      })
    }
  },
  mounted() {
    this.getIncome();
    this.withdrawList();
    this.token = 'Bearer ' + localStorage.token;
  }
};
</script>

<style scoped lang="scss">
.c-merchant {
  height: 330px;
    ul {
    display: flex;
    li {
      flex: 1;
      height: 141px;
      background: #fff;
      border-radius: 6px;
      margin: 0 24px 24px 0;
    }
    li:last-child {
      margin-right: 0;
    }
    .income-li {
      > p {
        color: #666;
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        text-align: center;
        margin-top: 42px;
      }
      > h3 {
        text-align: center;
        line-height: 26px;
        > b {
          color: #5769f0;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
        > span {
          color: #ffc000;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
      }
    }
    .order-li:nth-child(3),
    .order-li:nth-child(4) {
      .order-img {
        background: #eef2fc;
      }
    }
    .order-li {
      display: flex;
      align-items: center;
      .order-img {
        width: 66px;
        height: 66px;
        background: #fff2ca;
        border-radius: 100%;
        margin-left: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
          display: inline-block;
          background-image: url(../../../../assets/images/sprites-order.png);
          &.icon-order1 {
            width: 28px;
            height: 32px;
            background-position: -58px -66px;
          }
          &.icon-order2 {
            width: 28px;
            height: 32px;
            background-position: -10px -66px;
          }
          &.icon-money1 {
            width: 36px;
            height: 36px;
            background-position: -10px -10px;
          }
          &.icon-money2 {
            width: 36px;
            height: 36px;
            background-position: -66px -10px;
          }
        }
      }
      > h3 {
        flex: 1;
        margin-right: 29px;
        text-align: right;
        > b {
          font-size: 32px;
          color: #333;
          font-weight: bold;
          margin-top: 25px;
          line-height: 26px;
        }
        > p {
          font-size: 16px;
          color: #555;
          font-weight: 400;
          margin-top: 9px;
        }
      }
    }
  }
}
.dialogDetail-title {
  color: #555;
  font-size: 24px;
  font-weight: 500;
  line-height: 26px;
  margin-bottom: 24px;
  > b {
    color: #5769f0;
  }
}
</style>



