<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-button @click="tableAdd" type="primary" icon="el-icon-plus"></el-button>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-button type="primary" plain></el-button>
        <el-table
          :data="tableData"
          style="width: 100%;marginTop:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="id" label="ID" sortable></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="code" label="Code"></el-table-column>
          <el-table-column prop="time" label="" sortable width="180px"></el-table-column>
          <el-table-column label="" width="180px">
            <template slot-scope="scope">
              <a class="operation-btn" @click="dialogEdit=true"></a>
              <a class="operation-btn" @click="tableDel(scope.$index, scope.row)"></a>
              <a class="operation-btn" @click="dialogPrivilege=true"></a>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">11010</div>
          <div class="pagination-box">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="100"
              layout="prev, pager, next, jumper"
              :total="1000"
            ></el-pagination>
          </div>
        </div>
      </div>
      <div v-else class="table-default">
        <img src="../../../../assets/images/default.png" alt>
        <p></p>
      </div>
    </div>

    <!-- -->
    <el-dialog title="" :visible.sync="dialogAddRole" width="540px">
      <el-form :model="addForm">
        <el-form-item label="" label-width="80px">
          <el-select v-model="addForm.role" placeholder="">
            <el-option
              v-for="item in addForm.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Code" label-width="80px">
          <el-input v-model="addForm.code" autocomplete="off" placeholder="Code"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px">
          <el-input
            v-model="addForm.password"
            autocomplete="off"
            placeholder="6"
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddRole = false"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogEdit" width="540px">
      <el-form :model="form">
        <el-form-item label="" label-width="80px">
          <el-select v-model="form.role" placeholder="">
            <el-option
              v-for="item in form.options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Code" label-width="80px">
          <el-input v-model="form.code" autocomplete="off" placeholder="Code"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px">
          <el-input
            v-model="form.password"
            autocomplete="off"
            placeholder="6"
            show-password
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogEdit = false"></el-button>
        <el-button type="primary" @click="dialogEdit = false"></el-button>
      </div>
    </el-dialog>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogDel" width="540px">
      <div style="color:#555555; font-size:20px; margin:30px 0;"></div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogDel = false"></el-button>
        <el-button type="primary" @click="dialogDel = false"></el-button>
      </div>
    </el-dialog>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogPrivilege" width="540px">
      <div class="privilege-title">
        <i class="privilege-dot"></i>
        <span></span>
      </div>
      <div class="privilege-box">
        <el-tree
          :data="dataTree"
          show-checkbox
          default-expand-all
          node-key="id"
          ref="tree"
          highlight-current
        >
        <span class="custom-tree-node" slot-scope="{node,data}">
          <span :class="node.level==1 ? 'treeLabel' : ''" >{{data.label}}</span>
        </span>
        </el-tree>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogPrivilege = false"></el-button>
        <el-button type="primary" @click="dialogPrivilege = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogAddRole: false, //
      dialogEdit: false, //
      dialogDel: false, //
      dialogPrivilege: false, //
      timeStart: "",
      timeEnd: "",
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [
        {
          id: "1",
          name: "",
          code: "admin",
          time: "2019-03-02 11:11:42"
        },
        {
          id: "2",
          name: "",
          code: "will",
          time: "2019-03-02 11:11:42"
        }
      ],
      dataTree: [
        {
          label: "",
          children: [
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: "Token"
            }
          ]
        },
        {
          label: "",
          children: [
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: ""
            }
          ]
        },
        {
          label: "",
          children: [
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: ""
            },
            {
              label: "Token"
            }
          ]
        },
        {
          label: ""
        }
      ],
      form: {
        role: "",
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          },
          {
            value: "3",
            label: ""
          },
          {
            value: "4",
            label: ""
          },
          {
            value: "5",
            label: ""
          }
        ],
        code: "",
        password: ""
      },
      addForm: {
        role: "",
        options: [
          {
            value: "1",
            label: ""
          },
          {
            value: "2",
            label: ""
          },
          {
            value: "3",
            label: ""
          }
        ],
        code: "",
        password: ""
      }
    };
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    },
    //
    getTable() {
      let para = {
        page: this.page,
        name: this.filters.name
      };
      this.listLoading = true;
      getUserListPage(para).then(res => {
        this.total = res.data.total;
        this.users = res.data.users;
        this.listLoading = false;
      });
    },
    //
    tableAdd() {
      this.dialogAddRole = true;
    },
    addSubmit() {},
    //
    tableDel() {
      this.$confirm("?", "", {
        type: "warning"
      })
        .then(() => {
          this.listLoading = true;
          //NProgress.start();
          let para = { id: row.id };
          removeUser(para).then(res => {
            this.listLoading = false;
            //NProgress.done();
            this.$message({
              message: "",
              type: "success"
            });
            this.getUsers();
          });
        })
        .catch(() => {});
    }
  }
};
</script>


<style scoped lang="scss">
.privilege-title {
  width: 100%;
  height: 44px;
  background: #5769f0;
  display: flex;
  align-items: center;
  > i.privilege-dot {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: #fff;
    margin-left: 20px;
    border-radius: 100%;
  }
  > span {
    flex: 1;
    text-align: right;
    margin-right: 20px;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
  }
}
.privilege-box {
  width: 100%;
  height: 360px;
  overflow-y: auto;
  border: 1px solid #eeeeee;
  padding: 20px;
  .custom-tree-node {
    align-items: center;
    justify-content: space-between;
    padding-right: 8px;
    .treeLabel::before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 16px;
      height: 19px;
      margin-top: -2px;
      margin-right: 2px;
      background: url("../../../../assets/images/icon-role-add.png") no-repeat;
    }
  }
}
</style>



