<template>
  <div class="c-box">
    <div class="refresh">
      <div class="refreshInner" @click="refresh">
        <span></span>
        <i class="el-icon-refresh"></i>
      </div>
    </div>
    <div class="c-merchant" v-loading="dataLoading">
      <ul>
        <li class="income-li" v-for="(item, index) in incomeList" :key="index">
          <p>{{item.title}}</p>
          <h3>
            <b>{{item.number}}</b>
            <span>{{item.sum}}</span>
          </h3>
        </li>
      </ul>
      <ul>
        <li class="order-li" v-for="(item, index) in orderList" :key="index">
          <div class="order-img">
            <i class="icon" :class="item.iconImg"></i>
          </div>
          <h3>
            <b>{{item.number}}</b>
            <p>{{item.title}}</p>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-button type="primary" icon="el-icon-upload2" style="margin-right:28px;" @click="exportExcel"></el-button>
        </div>
        <div class="title-right">
          <el-date-picker
            style="width:360px;"
            v-model="dateArray"
            @change="pickTime"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span></span>
            <el-select v-model="flagType" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.id"
                :label="item.value"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <el-form :inline="true">
            <el-form-item>
              <el-input v-model="keywords" @keyup.enter.native="search" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-table
          v-loading="tableLoading"
          :data="tableData"
          style="width: 100%;margin-top:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column label="ID" width="70">
            <template slot-scope="scope">
              {{scope.$index+1}} 
            </template>
          </el-table-column>
          <el-table-column prop="realUnitPrice" label="" sortable></el-table-column>
          <el-table-column prop="typeTxt" label=""></el-table-column>
          <el-table-column prop="mobile" label="" width="160"></el-table-column>
          <el-table-column prop="nickName" label=""></el-table-column>
          <el-table-column prop="payTypeTxt" label=""></el-table-column>
          <el-table-column prop="createTime" label="" width="200" sortable></el-table-column>
          <el-table-column prop="payTime" label="" width="200" sortable></el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{total}}{{pages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="listPage"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-else class="table-default" v-loading="tableLoading">
        <img src="../../../../assets/images/default.png" alt>
        <p></p>
      </div>
    </div>
  </div>
</template>

<script>
import api from '../../../../assets/js/api.js'
import common from '../../../../assets/js/common.js';
export default {
  data() {
    return {
      beginTime: "",
      endTime: "",
      keywords: "",
      flagType: "",
      rows: 10,
      page: 1,
      pages: 1,
      total: 1,
      dataLoading: true,
      tableLoading: true,
      dateArray: "",
      tableData: [],
      incomeList: [],
      orderList: [],
      options: [
        {
          id: '',
          value: ""
        },
        {
          id: '1',
          value: ""
        },
        {
          id: '4',
          value: ""
        },
        {
          id: '5',
          value: ""
        },
        {
          id: '6',
          value: ""
        },
        {
          id: '7',
          value: ""
        }
      ]
    };
  },
  watch: {
    flagType(val) {
      this.tableLoading = true;
      this.page = 1;
      this.getList();
    }
  },
  methods: {
    getData() {    //
      api.findShopAccountTotal({
        userType: '',
        startTime: this.beginTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    refresh() {      //
      this.dataLoading = true;
      api.refreshShopAccountTotal({
        userType: '',
        startTime: this.beginTime,
        endTime: this.endTime
      }).then(res => {
        this.incomeCallback(res);
      }).catch(err => {});
    },
    incomeCallback(res) {
      if(res.code == 0) {
        let data = res.data, incomeList = [], orderList = [];
        incomeList = [
          {
              title: "  |  ",
              number: data.wxPayTotal || 0,
              sum: data.wxPayMoney || 0
          }, {
              title: "  |  ",
              number: data.yuePayTatal || 0,
              sum: data.yuePayMoney || 0
          }, {
              title: "  |  ",
              number: data.wxRefundTatal || 0,
              sum: data.wxRefundTatalMoney || 0
          },
          {
              title: "  |  ",
              number: data.yueRefundTatal || 0,
              sum: data.yueRefundTatalMoney || 0
          }
        ];
        orderList = [
          {
              iconImg: "icon-order1",
              number: data.toDayRefundTatal || 0,
              title: "()"
          },
          {
              iconImg: "icon-money1",
              number: data.toDayRefundTatalMoney || 0,
              title: "()"
          },
          {
              iconImg: "icon-order2",
              number: data.hisRefundTatal || 0,
              title: "()"
          },
          {
              iconImg: "icon-money2",
              number: data.hisRefundTatalMoney || 0,
              title: "()"
          }
        ];
        this.incomeList = incomeList;
        this.orderList = orderList;
        this.dataLoading = false;
      }
    },
    getList() {
      api.PlatformList({
        beginTime: this.beginTime,
        endTime: this.endTime,
        keywords: this.keywords,
        flagType: this.flagType,
        page: this.page,
        rows: this.rows
      }).then(res => {
        let data = res.data;
        if(res.code == 0 && data) {
          this.tableData = data.list ? this.flagTxt(data.list) : [];
          this.pages = data.pages;
          this.total = data.total;
          this.tableLoading = false;
        } 
      }).catch(err => {});
    },
    pickTime(e) {
      if(e == null) {
        this.beginTime = '';
        this.endTime = '';
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.dataLoading = true;
      this.tableLoading = true;
      this.page = 1;
      this.getData();
      this.getList();
    },
    exportExcel() {   //
      location.href = '/api/manage/Platform/export?beginTime='+this.beginTime+'&endTime='+this.endTime+'&keywords='+this.keywords+'&flagType='+this.flagType;
    },
    search() {      //
      this.tableLoading = true;
      this.page = 1;
      this.getList();
    },
    //
    empty() {
      this.tableLoading = true;
      this.keywords = '';
      this.page = 1;
      this.getList();
    },
    listPage(val) {    //
      this.tableLoading = true;
      this.page = val;
      this.getList();
    },
    flagTxt(list) {    //
      // flagType:(1  4  5 6 7)'
      list.forEach((e, i) => {
          e.typeTxt = (type => {
              if(type == 1) return '';
              if(type == 4) return '';
              if(type == 5) return '';
              if(type == 6) return '';
              if(type == 7) return '';
          })(e.flagType);
          e.payTypeTxt = (type => {
              if(type == 0) return '';
              if(type == 1) return '';
              if(type == 2) return '';
              if(type == 3) return '';
              if(type == 4) return '';
          })(e.payType);
          e.createTime = common.formatTime(new Date(e.createTime));
          e.payTime = common.formatTime(new Date(e.payTime));
      })
      return list;
    }
  },
  mounted() {
    this.getData();
    this.getList();
  }
};
</script>

<style scoped lang="scss">

.c-merchant {
  height: 330px;
  ul {
    display: flex;
    li {
      flex: 1;
      height: 141px;
      background: #fff;
      border-radius: 6px;
      margin: 0 24px 24px 0;
    }
    li:last-child {
      margin-right: 0;
    }
    .income-li {
      > p {
        color: #666;
        font-size: 16px;
        font-weight: 400;
        line-height: 26px;
        text-align: center;
        margin-top: 42px;
      }
      > h3 {
        text-align: center;
        line-height: 26px;
        > b {
          color: #5769f0;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
        > span {
          color: #ffc000;
          font-size: 20px;
          font-weight: 400;
          line-height: 60px;
        }
      }
    }
    .order-li:nth-child(3),
    .order-li:nth-child(4) {
      .order-img {
        background: #eef2fc;
      }
    }
    .order-li {
      display: flex;
      align-items: center;
      .order-img {
        width: 66px;
        height: 66px;
        background: #fff2ca;
        border-radius: 100%;
        margin-left: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
          display: inline-block;
          background-image: url(../../../../assets/images/sprites-order.png);
          &.icon-order1 {
            width: 28px;
            height: 32px;
            background-position: -58px -66px;
          }
          &.icon-order2 {
            width: 28px;
            height: 32px;
            background-position: -10px -66px;
          }
          &.icon-money1 {
            width: 36px;
            height: 36px;
            background-position: -10px -10px;
          }
          &.icon-money2 {
            width: 36px;
            height: 36px;
            background-position: -66px -10px;
          }
        }
      }
      > h3 {
        flex: 1;
        margin-right: 29px;
        text-align: right;
        > b {
          font-size: 32px;
          color: #333;
          font-weight: bold;
          margin-top: 25px;
          line-height: 26px;
        }
        > p {
          font-size: 16px;
          color: #555;
          font-weight: 400;
          margin-top: 9px;
        }
      }
    }
  }
}
.dialogForbidden-info {
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  margin: 30px 0;
}
</style>



