<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeSection"
            @change="pickTime"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            value-format="yyyy-MM-dd HH:mm:ss"
            :default-time="['00:00:00', '23:59:59']"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span></span>
            <el-select v-model="selectQuery" placeholder="" @change="getTableData('reset')">
              <el-option
                v-for="item in optionQuery"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                v-model="filters.name"
                @keyup.enter.native="getTableData('reset')"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData('reset')"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-button type="primary" plain @click="batchAgree"></el-button>
        <el-button type="primary" plain @click="batchDisagree"></el-button>
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%; margin-top:20px;"
          @selection-change="handleSelectionChange"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column type="selection" :selectable="checkboxInit" fixed="left" width="45"></el-table-column>
          <el-table-column prop="cashId" label="ID" fixed width="70"></el-table-column>
          <el-table-column prop="userName" label="" width="140"></el-table-column>
          <el-table-column prop="nickName" label="" width="120"></el-table-column>
          <el-table-column prop="mobile" label="" width="140"></el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.payType==''">
                  <i class="icon icon-weixin"></i>
                  <span></span>
                </template>
                <template v-if="scope.row.payType==''">
                  <i class="icon icon-yue"></i>
                  <span></span>
                </template>
                <template v-if="scope.row.payType==''">
                  <i class="icon icon-yue"></i>
                  <span></span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="createTime" label="" width="200" sortable></el-table-column>
          <el-table-column prop="accountName" label="" width="180"></el-table-column>
          <el-table-column prop="accountCardholderName" label="" width="100"></el-table-column>
          <el-table-column prop="bankCardName" label="" width="140"></el-table-column>
          <el-table-column prop="cashAmount" label="" width="130" sortable></el-table-column>
          <el-table-column prop="serviceAmount" label="" width="150" sortable></el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.applyStatus=='1'">
                  <span class="grgy"></span>
                </template>
                <template v-if="scope.row.applyStatus=='2'">
                  <span class="green"></span>
                </template>
                <template v-if="scope.row.applyStatus=='3'">
                  <span class="red"></span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="paidUserName" label="" width="100" sortable></el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.payStatus =='1'">
                  <span class="grgy"></span>
                </template>
                <template v-if="scope.row.payStatus =='2'">
                  <span class="green"></span>
                </template>
                <template v-if="scope.row.payStatus =='3'">
                  <span class="red"></span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" width="100" fixed="right">
            <template slot-scope="scope">
              <span v-if="scope.row.applyStatus=='1'">
                <el-button type="text" @click="consent(scope.row)"></el-button>
                <el-button type="text" @click="reject(scope.row)"></el-button>
              </span>
              <span v-else>
                <el-button type="text" disabled></el-button>
                <el-button type="text" disabled></el-button>
              </span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{total}}{{pages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="page"
              :page-count="pages"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-else class="table-default" v-loading="loadingTable">
        <img src="../../../../assets/images/default.png" alt>
        <p></p>
      </div>
    </div>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogFail" width="680px">
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="">
          <el-select v-model="formReject.select" placeholder="">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select==4">
          <el-input
            type="textarea"
            placeholder=""
            v-model="formReject.textarea"
            maxlength="30"
            show-word-limit
            style="width:402px;"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            :on-exceed="limitError" 
            :limit="5"
            ref="rejectUpload"
            action="#"
            list-type="picture-card"
            :on-success="imgSuccess"
            :http-request="beforeImgUploadHome"
            :on-preview="handlePictureCardPreview"
            :on-remove="handleRemove"
          >
            <i class="el-icon-plus"></i>
          </el-upload>
          <!--   -->
          <el-dialog :visible.sync="dialogVisible" :append-to-body="true">
            <img width="100%" :src="dialogImageUrl" alt>
          </el-dialog>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="formReject.isSend" label="1"></el-radio>
          <el-radio v-model="formReject.isSend" label="0"></el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFail = false"> </el-button>
        <el-button type="primary" @click="formRejectSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import api from "@/assets/js/api.js";
import formatTime from "@/assets/js/common.js";
export default {
  data() {
    return {
      timeSection: "",
      filters: {
        name: ""
      },
      optionQuery: [
        {
          value: "",
          label: ""
        },
        {
          value: "1",
          label: ""
        },
        {
          value: "2",
          label: ""
        },
        {
          value: "3",
          label: ""
        }
      ],
      selectQuery: "",
      tableData: [],
      page: 1, //
      pages: 0, //
      total: 0, //
      dialogImageUrl: "", //
      dialogImageArr: [],
      dialogVisible: false, //
      dialogFail: false, //
      multipleSelection: [], //checkbox 
      startTime: "", //
      endTime: "", //
      //
      formReject: {
        select: "",
        option: [
          {
            value: 1,
            label: ""
          },
          {
            value: 2,
            label: ""
          },
          {
            value: 3,
            label: ""
          },
          {
            value: 4,
            label: ""
          }
        ],
        textarea: "",
        isSend: "1"
      },
      cashId: "", //cashid
      loadingTable: false //
    };
  },
  mounted: function() {
    this.getTableData();
  },
  methods: {
    // 
    getTableData(types) {
      this.loadingTable = true;
      if (types == "reset") {
        this.page = 1;
      }
      api
        .searchUserAdvanceList({
          page: this.page,
          rows: this.rows,
          applyStatus: this.selectQuery,
          startTime: this.startTime,
          endTime: this.endTime,
          searchKey: formatTime.blank(this.filters.name)
        })
        .then(res => {
          let data = res.data;
          if (data) {
            for (let i = 0; i < data.list.length; i++) {
              data.list[i].createTime = formatTime.formatTime(
                new Date(data.list[i].createTime)
              );
              data.list[i].cashAmount = Number(data.list[i].cashAmount).toFixed(
                2
              );
              data.list[i].serviceAmount = Number(
                data.list[i].serviceAmount
              ).toFixed(2);
            }
            this.tableData = data.list ? data.list : [];
            this.total = data.total;
            this.pages = data.pages;
            this.loadingTable = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    operateList(arr) {
      arr.forEach((e, i) => {
        e.createTime = e.createTime.replace('.0', '');
      });
      return arr;
    },
    //
    pickTime(e) {
      if (e == null) {
        this.startTime = "";
        this.endTime = "";
      } else {
        this.startTime = e[0];
        this.endTime = e[1];
      }
      this.getTableData("reset");
    },
    //checkbox
    checkboxInit(row, index) {
      if (row.applyStatus == 1) {
        return 1; //
      } else {
        return 0; //
      }
    },
    //checkbox
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //
    handleCurrentChange(val) {
      this.page = val;
      this.getTableData();
    },
    //
    consent(rowData) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.postConsent(rowData);
          this.getTableData();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    postConsent(choseData) {
      let arr = [];
      if (choseData instanceof Array) {
        arr = choseData;
      } else {
        arr.push(choseData.cashId);
      }
      let cashId = "";
      if (arr.length > 1) {
        cashId = arr.join(",");
      } else {
        cashId = arr[0];
      }
      // let params = new URLSearchParams();
      // params.append("ids", cashId);
      let obj = {
        applyStatus: 2,
        ids: cashId
      };
      api
        .batchApplyPass(obj)
        .then(res => {
          if (res.message) {
            this.$message({
              type: "success",
              message: "!"
            });
          } else {
            this.$message.error("");
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    batchAgree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            let arr = [];
            this.multipleSelection.forEach((v, k) => {
              arr.push(v.cashId);
            });
            this.postConsent(arr);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$notify({
          title: "",
          message: "!",
          type: "warning"
        });
      }
    },
    //
    empty() {
      this.filters.name = "";
      this.getTableData();
    },
    //
    reject(row) {
      this.dialogFail = true;
      this.cashId = row.cashId;
      this.formReject.select = "";
      this.formReject.isSend = "1";
      this.formReject.textarea = "";
      this.dialogImageUrl = "";
      this.dialogImageArr = [], 
      this.$refs.rejectUpload.clearFiles();
    },
    //
    formRejectSubmit() {
      var arr = [];
      arr.push(this.cashId);
      this.postReject(arr);
    },
    //
    postReject(arr) {
      if (!this.formReject.select)
        return this.$message.warning("");
      if (this.formReject.select == 4 && !this.formReject.textarea)
        return this.$message.warning("");
      let text = "";
      if(this.formReject.select != 4){
        text = this.formReject.option[this.formReject.select-1].label;
      }else{
        text = this.formReject.textarea;
      }  
      let urlArr = [];    
      this.dialogImageArr.forEach((value, i) => {
        urlArr.push(value.imgUrl);
      });
      this.dialogImageUrl = urlArr.join(",");  
      var param = {
        content: text,
        imageUrl: this.dialogImageUrl,
        isSend: this.formReject.isSend,
        items: arr
      }; 
      api
        .batchApplyReject(param)
        .then(res => {
          if (res.code == 0) {
            this.$message.success("");
            this.dialogFail = false;
            this.getTableData();
          } else {
            this.$message.error(res.message);
          }
          this.dialogFail = false;
        })
        .catch(err => {
          this.dialogFail = false;
          console.log(err);
        });
    },
    //
    batchDisagree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            let arr = [];
            this.multipleSelection.forEach((v, k) => {
              arr.push(v.cashId);
            });
            this.postReject(arr);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$notify({
          title: "",
          message: "!",
          type: "warning"
        });
      }
    },
    //
    descInput() {
      this.formReject.remnant = this.formReject.textarea.length;
    },
    //
    limitError() {
      this.$message.error({
        message: ""
      });
    },
    //
    imgSuccess() {},
    beforeImgUploadHome(param) {
      let that = this;
      uploadImg(param.file).then(imgUrl => {
        that.dialogImageUrl = imgUrl;
        param.file.imgUrl = imgUrl;
        that.dialogImageArr.push(param.file);
      });
      return false;
    },
    handleRemove(file, fileList) {
      this.dialogImageArr = this.dialogImageArr.filter(ele => {
        ele.uid == file.uid;
      });
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
  }
};

var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    // let that = _this;
    form.append("file", file);
    api
      .upload(form)
      .then(res => {
        return resolve(res.data.picUrl);
      })
      .catch(err => {
        console.log(err);
      });
  });
};
</script>

<style scoped lang="scss">
.dialogForbidden-info {
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px;
  margin: 30px 0;
}

.form-remnant {
  position: absolute;
  top: 20px;
  color: #999;
  text-align: right;
  width: 392px;
}
</style>



