<template>
  <div class="c-box">
    <div class="c-merchant">
      <ul>
        <li class="order-li" v-for="(item, index) in orderList" :key="index">
          <div class="order-img">
            <i class="icon" :class="item.iconImg"></i>
          </div>
          <h3>
            <b v-if="item.number == null||item.number == ''">0</b>
            <b v-else>{{item.number}}</b>
            <p>{{item.title}}</p>
          </h3>
        </li>
      </ul>
    </div>
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-button
            @click="exportExcel"
            type="primary"
            icon="el-icon-upload2"
            style="margin-right:28px;"
          ></el-button>
          <el-date-picker
            v-model="timeSection"
            @change="pickTime"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            :default-time="['00:00:00', '23:59:59']"
            value-format="yyyy-MM-dd HH:mm:ss"
            unlink-panels
          ></el-date-picker>
          <div class="selectBox">
            <span></span>
            <el-select v-model="selectScreen" placeholder="" @change="getTableData('reset')">
              <el-option
                v-for="item in optionScreen"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input
                v-model="filters.name"
                @keyup.enter.native="getTableData('reset')"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="getTableData('reset')"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button @click="empty"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box" v-if="tableData.length>0">
        <el-button type="primary" plain @click="batchAgree"></el-button>
        <el-button type="primary" plain @click="batchDisagree"></el-button>
        <el-table
          v-loading="loadingTable"
          :data="tableData"
          style="width: 100%;margin-top:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" :selectable="checkboxInit" width="40" fixed></el-table-column>
          <el-table-column type="expand" width="40" fixed>
            <template slot-scope="props">
              <table class="table-expand">
                <thead>
                  <tr>
                    <td width="200px"></td>
                    <td width="200px"></td>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item, index) in props.row.orderItems" :key="index">
                    <td>{{item.goodsPrice}}</td>
                    <td>{{item.goodsSkuName}}</td>
                  </tr>
                </tbody>
              </table>
            </template>
          </el-table-column>
          <el-table-column prop="outTradeNo" label="" width="140"></el-table-column>
          <el-table-column prop="createTime" label="" width="170" sortable></el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.orderType==1"></span>
              <span v-if="scope.row.orderType==2"></span>
              <span v-if="scope.row.orderType==3"></span>
            </template>
          </el-table-column>
          <el-table-column prop="totalFee" label="" width="140" sortable></el-table-column>
          <el-table-column prop="userName" label="" width="150"></el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.status=='0'">
                  <span class="green"></span>
                </template>
                <template v-if="scope.row.status=='1'">
                  <span class="red"></span>
                </template>
                <template v-if="scope.row.status=='2'">
                  <span class="grgy"></span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <span v-if="scope.row.type==1"></span>
              <span v-if="scope.row.type==2">APP</span>
            </template>
          </el-table-column>
          <el-table-column prop="shop.shopName" label="" width="160"></el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <div class="table-scope">
                <template v-if="scope.row.payType=='0'">
                  <i class="icon icon-zengsong"></i>
                  <span></span>
                </template>
                <template v-if="scope.row.payType=='1'">
                  <i class="icon icon-jinbi"></i>
                  <span></span>
                </template>
                <template v-if="scope.row.payType=='2'">
                  <i class="icon icon-weixin"></i>
                  <span></span>
                </template>
                <template v-if="scope.row.payType=='3'">
                  <i class="icon icon-zhifubao"></i>
                  <span></span>
                </template>
                <template v-if="scope.row.payType=='4'">
                  <i class="icon icon-yue"></i>
                  <span></span>
                </template>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="applyReason" label="" width="120"></el-table-column>
          <el-table-column prop="otherReason" label="" width="120"></el-table-column>
          <el-table-column label="" width="100" fixed="right">
            <template slot-scope="scope">
              <span v-if="scope.row.status=='0'">
                <el-button type="text" @click="consent(scope.row)"></el-button>
                <el-button type="text" @click="reject(scope.row)"></el-button>
              </span>
              <span v-else>
                <el-button type="text" disabled></el-button>
                <el-button type="text" disabled></el-button>
              </span>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">{{total}}{{pages}}</div>
          <div class="pagination-box">
            <el-pagination
              @current-change="handleCurrentChange"
              :current-page.sync="page"
              :page-count="pages"
              :page-size="rows"
              layout="prev, pager, next, jumper"
              :total="total"
            ></el-pagination>
          </div>
        </div>
      </div>
      <!--  -->
      <div v-else class="table-default" v-loading="loadingTable">
        <img src="../../../../assets/images/default.png" alt>
        <p></p>
      </div>
    </div>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogFail" width="680px">
      <el-form ref="formReject" label-width="100px">
        <el-form-item label="">
          <el-select v-model="formReject.select" placeholder="">
            <el-option
              v-for="item in formReject.option"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label v-if="this.formReject.select==4">
          <el-input
            type="textarea"
            placeholder=""
            v-model="formReject.textarea"
            maxlength="30"
            show-word-limit
            style="width:402px;"
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
            <el-upload
              action="https://jsonplaceholder.typicode.com/posts/"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt>
            </el-dialog>
        </el-form-item>-->
        <el-form-item label="">
          <el-radio v-model="formReject.isSend" label="1"></el-radio>
          <el-radio v-model="formReject.isSend" label="0"></el-radio>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFail = false"> </el-button>
        <el-button type="primary" @click="formRejectSubmit"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

  <script>
import api from "@/assets/js/api.js";
import formatTime from "@/assets/js/common.js";
export default {
  data() {
    return {
      dialogFail: false,
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [],
      page: 1, //
      pages: 0, //
      rows: 10, //
      total: 0, //
      orderList: [],
      selectScreen: "",
      optionScreen: [
        {
          value: "",
          label: ""
        },
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        }
      ],
      //
      dialogImageUrl: "",
      dialogVisible: false,
      loadingTable: false,
      loadingBtn: false,
      timeSection: "",
      _beginTime: "", //
      get beginTime() {
        return this._beginTime;
      },
      set beginTime(value) {
        this._beginTime = value;
      },
      endTime: "", //
      multipleSelection: [], //checkbox 
      //
      formReject: {
        select: "",
        option: [
          {
            value: 1,
            label: ""
          },
          {
            value: 2,
            label: ""
          },
          {
            value: 3,
            label: ""
          },
          {
            value: 4,
            label: ""
          }
        ],
        textarea: "",
        isSend: "1"
      }
    };
  },
  mounted() {
    this.getTableData();
    this.getList();
  },
  methods: {
    //
    getList() {
      api
        .getTableList()
        .then(res => {
          if (res.code == 1) {
            let data = res.data,
              orderList = [];
            orderList = [
              {
                iconImg: "icon-order1",
                number: data[0].todayFefundTotal,
                title: "()"
              },
              {
                iconImg: "icon-money1",
                number: data[0].todayFefundAmount,
                title: "()"
              },
              {
                iconImg: "icon-order2",
                number: data[0].historicalFefundTotal,
                title: "()"
              },
              {
                iconImg: "icon-money2",
                number: data[0].historicalFefundAmount,
                title: "()"
              }
            ];
            this.orderList = orderList;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    getTableData(types) {
      this.loadingTable = true;
      if (types == "reset") {
        this.page = 1;
      }
      api
        .getList({
          page: this.page,
          rows: this.rows,
          status: this.selectScreen,
          beginTime: this.beginTime,
          endTime: this.endTime,
          keywords: this.filters.name.trim()
        })
        .then(res => {
          let data = res.data;
          if (data) {
            for (let i = 0; i < data.list.length; i++) {
              data.list[i].createTime = formatTime.formatTime(
                new Date(data.list[i].createTime)
              );
              data.list[i].totalFee = Number(data.list[i].totalFee).toFixed(
                2
              );
              for(let j = 0; j < data.list[i].orderItems.length; j++){
                data.list[i].orderItems[j].goodsPrice = Number(data.list[i].orderItems[j].goodsPrice).toFixed(
                  2
                );
              }
            }
            this.tableData = data.list ? data.list : [];
            this.total = data.total;
            this.page = data.page;
            this.rows = data.rows;
            this.pages = data.pages;
            this.loadingTable = false;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    consent(rowData) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.postAgreeRefund(rowData);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    postAgreeRefund(choseData) {
      let arr = [];
      if (choseData instanceof Array) {
        arr = choseData;
      } else {
        arr.push(choseData.id);
      }
      let id = "";
      if (arr.length > 1) {
        id = arr.join(",");
      } else {
        id = arr[0];
      }
      let params = {
        ids: id
      };
      api
        .agreeRefund(params)
        .then(res => {
          if (res.message) {
            this.$message({
              type: "success",
              message: "!"
            });
            this.getTableData();
          } else {
            this.$message.error("");
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //
    batchAgree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            let arr = [];
            this.multipleSelection.forEach((v, k) => {
              arr.push(v.id);
            });
            this.postAgreeRefund(arr);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$notify({
          title: "",
          message: "!",
          type: "warning"
        });
      }
    },
    //
    reject(row) {
      this.dialogFail = true;
      this.id = row.id;
      this.formReject.select = "";
      this.formReject.textarea = "";
      this.formReject.isSend = "1";
    },
    //
    formRejectSubmit() {
      console.log(this.formReject.select);
      let content = "";
      if (!this.formReject.select)
        return this.$message.error("");
      if (this.formReject.select == 4 && !this.formReject.textarea)
        return this.$message.error("");
      if (this.formReject.select == 4) {
        content = this.formReject.textarea;
      }else{
        content = this.formReject.option[this.formReject.select-1].label;
      } 
      let arr = [];
      arr.push(this.id);
      this.postRefuseRefund(arr, content);
    },
    //
    postRefuseRefund(arr, content) { 
      let id = "";
      if (arr.length > 1) {
        id = arr.join(",");
      } else {
        id = arr[0];
      }
      api
        .refuseRefund({
          ids: id,
          content: content,
          isSend: this.formReject.isSend
        })
        .then(res => {
          if (res.code == 0) {
            this.$message.success("");
            this.dialogFail = false;
            this.getTableData();
          } else {
            this.dialogFail = false;
            this.$message.error(res.message);
          }
        })
        .catch(err => {
          this.dialogFail = false;
          this.$message.error(err);
        });
    },
    //
    disagreeByGroup() {
      let arr = [];
      this.multipleSelection.forEach((v, k) => {
        arr.push(v.id);
      });
      this.postRefuseRefund(arr);
    },
    //
    batchDisagree() {
      if (this.multipleSelection.length > 0) {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            this.disagreeByGroup();
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$notify({
          title: "",
          message: "!",
          type: "warning"
        });
      }
    },
    //
    handleCurrentChange(val) {
      this.getTableData();
    },
    //
    exportExcel() {
      let params = {},
        str = "";
      params = {
        excle: true,
        status: this.selectScreen,
        beginTime: this.beginTime,
        endTime: this.endTime,
        keywords: this.filters.name.trim()
      };
      for (let key in params) {
        str += key + "=" + params[key] + "&";
      }
      str = str.substring(0, str.length - 1);
      location.href = "/api/manage/payRefund/export?" + str;
    },
    //
    empty() {
      this.filters.name = "";
      this.getTableData("reset");
    },
    //
    pickTime(e) {
      if (e == null) {
        this.beginTime = "";
        this.endTime = "";
      } else {
        this.beginTime = e[0];
        this.endTime = e[1];
      }
      this.getTableData("reset");
    },
    //checkbox
    checkboxInit(row, index) {
      if (row.status == 0) {
        return 1; //
      } else {
        return 0; //
      }
    },
    //checkbox
    handleSelectionChange(val) {
      this.multipleSelection = val;
    }
    // 
    // handleRemove(file, fileList) {
    //   console.log(file, fileList);
    // }
    // handlePictureCardPreview(file) {
    //   this.dialogImageUrl = file.url;
    //   this.dialogVisible = true;
    // }
  }
};
</script>

<style scoped lang="scss">
.c-merchant ul {
  display: flex;
  li {
    flex: 1;
    height: 141px;
    background: #fff;
    border-radius: 6px;
    margin: 0 24px 24px 0;
  }
  li:last-child {
    margin-right: 0;
  }
  .income-li {
    > p {
      color: #666;
      font-size: 16px;
      font-weight: 400;
      line-height: 26px;
      text-align: center;
      margin-top: 42px;
    }
    > h3 {
      text-align: center;
      line-height: 26px;
      > b {
        color: #5769f0;
        font-size: 20px;
        font-weight: 400;
        line-height: 60px;
      }
      > span {
        color: #ffc000;
        font-size: 20px;
        font-weight: 400;
        line-height: 60px;
      }
    }
  }
  .order-li:nth-child(3),
  .order-li:nth-child(4) {
    .order-img {
      background: #eef2fc;
    }
  }
  .order-li {
    display: flex;
    align-items: center;
    .order-img {
      width: 66px;
      height: 66px;
      background: #fff2ca;
      border-radius: 100%;
      margin-left: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      i {
        display: inline-block;
        background-image: url(../../../../assets/images/sprites-order.png);
        &.icon-order1 {
          width: 28px;
          height: 32px;
          background-position: -58px -66px;
        }
        &.icon-order2 {
          width: 28px;
          height: 32px;
          background-position: -10px -66px;
        }
        &.icon-money1 {
          width: 36px;
          height: 36px;
          background-position: -10px -10px;
        }
        &.icon-money2 {
          width: 36px;
          height: 36px;
          background-position: -66px -10px;
        }
      }
    }
    > h3 {
      flex: 1;
      margin-right: 29px;
      text-align: right;
      > b {
        font-size: 32px;
        color: #333;
        font-weight: bold;
        margin-top: 25px;
        line-height: 26px;
      }
      > p {
        font-size: 16px;
        color: #555;
        font-weight: 400;
        margin-top: 9px;
      }
    }
  }
}

.form-remnant {
  position: absolute;
  top: 20px;
  color: #999;
  text-align: right;
  width: 392px;
}
</style>



