<template>
  <div class="c-box">
    <div class="c-table">
      <div class="table-title">
        <div class="title-left">
          <el-date-picker
            v-model="timeStart"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            prefix-icon="el-icon-date"
            :default-time="['12:00:00']"
          ></el-date-picker>
        </div>
        <div class="title-right">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary"></el-button>
            </el-form-item>
            <el-form-item>
              <el-button></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
      <div class="table-box">
        <el-button type="primary" plain></el-button>
        <el-table
          :data="tableData"
          style="width: 100%;marginTop:20px;"
          :default-sort="{prop: 'date', order: 'descending'}"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="id" label="ID" sortable></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="loginTime" label="" sortable></el-table-column>
          <el-table-column prop="lastTime" label="" sortable></el-table-column>
          <el-table-column prop="number" label="" sortable></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <a class="operation-btn" @click="dialogForbidden=true"></a>
              <a class="operation-btn"></a>
            </template>
          </el-table-column>
        </el-table>
        <div class="table-pagination">
          <div class="pagination-info">11010</div>
          <div class="pagination-box">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page.sync="currentPage"
              :page-size="100"
              layout="prev, pager, next, jumper"
              :total="1000"
            ></el-pagination>
          </div>
        </div>
      </div>
    </div>
    <!-- -->
    <el-dialog title="" :visible.sync="dialogForbidden" width="540">
      <div class="dialogForbidden-info"></div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogForbidden = false"> </el-button>
        <el-button type="primary" @click="dialogForbidden = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "User",
  data() {
    return {
      dialogForbidden: false,
      timeStart: "",
      filters: {
        name: ""
      },
      currentPage: 1,
      tableData: [
        {
          id: "1",
          name: "",
          phone: "18677776666",
          loginTime: "2018-10-23 09:17:30",
          lastTime: "2019-03-02 11:11:42",
          number: "210"
        }
      ]
    };
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
    }
  }
};
</script>

<style scoped lang="scss">
.dialogForbidden-info{
  color: #555;
  font-size: 20px;
  font-weight: 400;
  line-height: 26px; 
  margin: 30px 0;
}
</style>



